import React, { Component } from 'react';
import { Table, Popconfirm } from 'antd';
// import DataSource from 'Resource/json/statistics.json';
import styles from '../index.less';
import Network from 'Components/Network';
import { Spin, message } from 'antd';
import { withRouter } from 'react-router-dom';
import { compose, graphql, withApollo } from 'react-apollo';
import {
  AssessmentHistory,
  DeleteComponentTroubleResultMessage
} from 'Api/graphql/alertRecord.graphql';

@withRouter
class TableFormat extends Component {
  constructor(props) {
    super(props);
    this.columns = [
      {
        title: '预警编号',
        dataIndex: 'number',
        key: 'number'
      },
      {
        title: '发生时间',
        dataIndex: 'reportTime',
        key: 'reportTime'
      },
      {
        title: '设备对象',
        dataIndex: 'device',
        key: 'device',
        render: (text,item) => {
          return (
            <span>
              {item.device}  ({item.fanNumber}/{item.motorNumber})
            </span>
          );
        }
      },
      {
        title: '预警等级',
        dataIndex: 'statusDisplayName',
        key: 'statusDisplayName',
        render: (text, item) => {
          return (
            <span style={{ color: this.getColor(item.status) }}>{text}</span>
          );
        }
      },
      {
        title: '持续时长',
        dataIndex: 'duration',
        key: 'duration'
      },
      {
        title: '预警原因',
        dataIndex: 'reason',
        key: 'reason'
      },
      {
        title: '处理状态',
        dataIndex: 'reportStatus',
        key: 'reportStatus'
      },
      {
        title: '操作',
        // dataIndex: 'occurrenceTime',
        key: 'operation',
        render: text => {
          return (
            <span>
              <span
                className={styles.btn}
                style={{ marginRight: 8 }}
                onClick={() => this.go(text)}
              >
                详情/反馈
              </span>
              <Popconfirm
                overlayStyle={{ width: 200 }}
                placement={'top'}
                title={'确认删除吗？'}
                onConfirm={() => this.delete(text)}
              >
                <span className={styles.btn}>删除</span>
              </Popconfirm>
            </span>
          );
        }
      }
    ];
  }
  getColor = type => {
    return {
      warning: '#FFBA00',
      fault: '#F1677D'
    }[type];
  };
  //跳到预警详情
  go = text => {
    this.props.history.push({
      pathname: '/AlertDetail',
      state: { id: text.key, iconType: text.iconType }
    });
  };
  //删除
  delete = text => {
    this.props
      .deleteComponentTroubleResultMessage({ variables: { key: text.key } })
      .then(res => {
        if (res.data.deleteComponentTroubleResultMessage.ok) {
          message.success('删除成功！');
          this.props.data.refetch();
        } else {
          message.error(res.data.deleteComponentTroubleResultMessage.errorMsg);
        }
      });
  };
  render() {
    const { data } = this.props;
    if (data.error)
      return (
        <div className={styles.tip}>
          <Network title={'网络错误!'} />
        </div>
      );
    if (data.loading)
      return (
        <div className={styles.tip}>
          <Spin />
        </div>
      );
    const { assessmentHistory } = data;
    return (
      <Table
        columns={this.columns}
        dataSource={assessmentHistory}
        bordered={true}
        pagination={{ pageSize: 11, showQuickJumper: true }}
      />
    );
  }
}
export default compose(
  withApollo,
  graphql(AssessmentHistory, {
    options: props => {
      return {
        variables: {
          fanId: props.fanId,
          reportStatus: props.reportStatus,
          statusId: props.statusId,
          reasonId: props.reasonId,
          startTime: props.startTime,
          endTime: props.endTime,
          parentAreaId: props.parentAreaId,
          sonAreaId: props.sonAreaId
        },
        errorPolicy: 'all',
        fetchPolicy: 'network-only',
        notifyOnNetworkStatusChange: true
      };
    }
  }),
  graphql(DeleteComponentTroubleResultMessage, {
    name: 'deleteComponentTroubleResultMessage'
  })
)(TableFormat);
