import React, { Component } from 'react';
import styles from './index.less';
import Chart from './chart';
import { Radio, Spin } from 'antd';
// import DataSource from 'Resource/json/trend.json';
import eventObject from 'Configs/eventSignal';
import { compose, graphql, withApollo } from 'react-apollo';
import { CheckpointParamType } from 'Api/graphql/deviceDetail.graphql';
import Network from 'Components/Network';
import Empty from 'Components/Empty';
import RadioFormat from 'Components/Radio';
import classNames from 'classnames';

const cx = classNames.bind(styles);
const RadioGroup = Radio.Group;
const RadioButton = Radio.Button;

class Trend extends Component {
  constructor(props) {
    super(props);
    this.state = {
      paramTypeId: '',
      dateType: 'day'
    };
  }
  componentWillMount() {
    eventObject.scale.add(this.scale);
    this.formatData(this.props);
  }
  componentWillReceiveProps(nextProps) {
    this.formatData(nextProps);
  }
  componentWillUnmount() {
    eventObject.scale.remove(this.scale);
  }
  formatData = props => {
    const { data } = props;
    if (data.error || data.loading) return null;
    const { checkpointParamType } = data;
    if (checkpointParamType.length) {
      this.setState({ paramTypeId: checkpointParamType[0].id });
    }
  };
  scale = params => {
    if (params) {
      this.container.style.display = 'none';
    } else {
      this.container.style.display = 'block';
    }
  };
  //单选改变
  onChange = e => {
    this.setState({ paramTypeId: e.target.value });
  };
  renderRadio = () => {
    const { data } = this.props;
    if (data.error) return <Network title={'网络错误'} />;
    if (data.loading) return <Spin />;
    const { checkpointParamType } = data;
    return checkpointParamType.map((value, index) => {
      return (
        <RadioButton key={index} value={value.id} className={styles.radio}>
          <span className={styles.content}>
            <span className={cx(styles[value.name], styles.icon)} />
            <div style={{ textAlign: 'center', width: 73 }}>
              {value.displayName}
            </div>
          </span>
        </RadioButton>
      );
    });
  };
  //频率切换
  changePickerType = e => {
    this.setState({ dateType: e.target.value });
  };
  render() {
    return (
      <div
        className={styles.trendContainer}
        ref={ref => (this.container = ref)}
      >
        <div className={styles.top}>
          <span className={styles.title}>实时数据点位趋势</span>
          <RadioFormat
            defaultValue={this.state.dateType}
            data={[
              { id: 'day', displayName: '当日' },
              { id: 'week', displayName: '7日' },
              { id: 'month', displayName: '30日' }
            ]}
            change={this.changePickerType}
          />
        </div>
        <div className={styles.bottom}>
          <RadioGroup
            onChange={this.onChange}
            value={this.state.paramTypeId}
            style={{ paddingLeft: 10 }}
          >
            {this.renderRadio()}
          </RadioGroup>
          {this.state.paramTypeId ? (
            <Chart {...this.props} {...this.state} />
          ) : (
            <Empty />
          )}
        </div>
      </div>
    );
  }
}
export default compose(
  withApollo,
  graphql(CheckpointParamType, {
    options: props => {
      return {
        variables: { fanId: props.fanId },
        errorPolicy: 'all',
        fetchPolicy: 'cache-and-network'
      };
    }
  })
)(Trend);
