import React, { Component } from 'react';
import styles from './index.less';
import { withRouter } from 'react-router-dom';

@withRouter
class List extends Component {
  //进入详情
  go = (id, iconType) => {
    this.props.history.push({
      pathname: '/AlertDetail',
      state: { id: id, iconType: iconType }
    });
  };
  render() {
    if (!this.props.data.length)
      return <div style={{ textAlign: 'center' }}>暂无数据！</div>;
    return (
      <div style={{ padding: '0 16px 40px 16px' }}>
        {this.props.data.map((value, index) => {
          return (
            <div
              className={styles.item}
              key={index}
              onClick={() => this.go(value.id, value.iconType)}
            >
              <div className={styles.flex}>
                <span className={styles[value.isRead ? 'circle' : 'dot']} />
                <span className={styles[value.status.name]}>
                  {value.status.displayName}
                </span>
              </div>
              <span className={styles.time}>{value.time}</span>
              <span>
                设备编码：{value.fanNumber} / {value.motorNumber}
              </span>
              <span>设备名称：{value.name}</span>
              <span>所属区域：{value.area}</span>
              <span>预警原因：{value.reason}</span>
              <div className={styles.line} />
            </div>
          );
        })}
      </div>
    );
  }
}
export default List;
