import React, { Component } from 'react';
import { Checkbox } from 'antd';
import styles from './index.less';

export default class CheckBoxFormat extends Component {
  render() {
    const { data, value, onChange, paramId, style } = this.props;
    return (
      <div className={styles.wrap} style={style}>
        <Checkbox.Group
          style={{ width: '100%' }}
          onChange={onChange}
          value={value}
        >
          {data &&
            data.map((value, index) => {
              return (
                <Checkbox
                  value={value.id}
                  key={index}
                  disabled={paramId === value.id}
                >
                  {value.displayName}
                </Checkbox>
              );
            })}
        </Checkbox.Group>
      </div>
    );
  }
}
