import React, { Component } from 'react';
import styles from './index.less';
import { Radio } from 'antd';
import Statistics from './Statistics';
import Trend from './Trend';

const RadioGroup = Radio.Group;

export default class Content extends Component {
  constructor(props) {
    super(props);
    this.state = { value: 1 };
  }
  change = e => {
    this.setState({ value: e.target.value });
  };
  render() {
    // console.log(this.props.leftParamId)
    return (
      <div className={styles.container}>
        <div>
          <div className={styles.flex} style={{ marginBottom: 24 }}>
            <RadioGroup defaultValue={this.state.value} onChange={this.change}>
              <Radio value={1}>特征趋势</Radio>
              <Radio value={2}>异常预警</Radio>
            </RadioGroup>
          </div>
          {this.state.value === 2 ? (
            <Statistics {...this.props} />
          ) : (
            this.props.leftParamId && <Trend {...this.props} />
          )}
        </div>
      </div>
    );
  }
}
