import React, { Component } from 'react';
import ReactEcharts from 'echarts-for-react';
import styles from '../index.less';
import color from 'Configs/color';

export default class LineChart extends Component {
  formatOption = data => {
    return {
      color: color,
      grid: {
        left: 60,
        right: 60
      },
      legend: {
        show: true,
        type: 'scroll',
        pageTextStyle: {
          color: '#fff'
        },
        pageIconColor: '#fff',
        pageIconSize: 10
      },
      xAxis: [
        {
          type: 'category',
          data: data.xAxis || [],
          // data: [],
          symbolOffset: ['50%', 0]
        },
        {
          axisLine: {
            show: false
          }
        }
      ],
      yAxis: data.data.length
        ? this.getUnit(data.data)
        : [
            {
              type: 'value'
            }
          ],
      tooltip: {
        show: true,
        trigger: 'axis'
      },
      dataZoom: [
        {
          type: 'slider',
          show: true,
          textStyle: {
            color: '#fff'
          },
          dataBackground: {
            lineStyle: {
              color: '#fff'
            },
            areaStyle: { color: 'rgba(255,255,255,1)' }
          }
        },
        {
          type: 'inside'
        }
      ],
      series: data.data.length ? this.getSeriesData(data.data) : []
      // series: []
    };
  };
  getSeriesData = data => {
    let arr = [];

    data.map((value, index) => {
      let obj = {
        data: value.data,
        type: 'line',
        // symbol: 'none',
        smooth: true,
        name: value.name,
        yAxisIndex: index > 0 ? 1 : 0
      };
      if (data.length > 1) {
        if (data[0].unit === data[1].unit) {
          obj.yAxisIndex = 0;
        } else {
          obj.yAxisIndex = index > 0 ? 1 : 0;
        }
      } else {
        obj.yAxisIndex = 0;
      }
      return arr.push(obj);
    });
    return arr;
  };
  getUnit = data => {
    let arr = [];
    if (data.length) {
      arr.push({
        type: 'value',
        name: data[0].unit
      });
      if (data.length > 1) {
        if (data[0].unit !== data[1].unit) {
          arr.push({
            type: 'value',
            name: data[1].unit,
            nameTextStyle: {
              padding: [0, -25, 0, 0]
            }
          });
        }
      }
    }
    return arr;
  };
  render() {
    const { title, paramData } = this.props.data;
    const { style } = this.props;
    let option = this.formatOption(paramData);

    return (
      <div className={styles.chart} style={style}>
        <span className={styles.title}>{title}</span>
        <ReactEcharts
          option={option}
          style={{ width: '100%', height: 'calc(100% - 54px)' }}
          theme="my_theme"
          notMerge={true}
        />
      </div>
    );
  }
}
