import React, { Component } from 'react';
import styles from '../index.less';
import ReactEcharts from 'echarts-for-react';
// import DataSource from 'Resource/json/health.json';
import Network from 'Components/Network';
import { Spin } from 'antd';
import { compose, graphql, withApollo } from 'react-apollo';
import { HealthIndexRadar } from 'Api/graphql/evaluation.graphql';

class RadarChart extends Component {
  constructor(props) {
    super(props);
    this.state = {
      data: { indicator: [], data: [] }
    };
  }

  formatOption = data => {
    return {
      tooltip: {
        show: true,
        trigger: 'item'
      },
      color: this.getColor(data.healthIndex),
      radar: {
        name: {
          textStyle: {
            color: '#fff',
            fontSize: 16
          }
        },
        indicator: data.indicator.length
          ? data.indicator
          : [
              { name: '预警', max: 100 },
              { name: '温度', max: 100 },
              { name: '效率', max: 100 },
              { name: '在役时间', max: 100 },
              { name: '振动', max: 100 }
            ],
        splitArea: {
          areaStyle: ['rgba(255,255,255,0)', 'rgba(255,255,255,0)']
        },
        radius: '64%'
      },
      series: {
        name: this.props.time,
        type: 'radar',
        data: [
          {
            value: data.data,
            areaStyle: {
              normal: {
                opacity: 0.3
              }
            }
          }
        ]
      }
    };
  };
  getColor = score => {
    let color;
    if (score < 60) {
      color = '#F1677D';
    } else if (score > 60 && score < 80) {
      color = '#ffae5c';
    } else {
      color = '#00FFD6';
    }
    return color;
  };
  render() {
    const { data } = this.props;
    if (data.error) return <Network />;
    if (data.loading) return <Spin />;
    const { healthIndexRadar } = data;
    let option = this.formatOption(healthIndexRadar);
    return (
      <div className={styles.flexColumn}>
        <ReactEcharts
          option={option}
          style={{ width: '100%', height: '80%' }}
          theme="my_theme"
          notMerge={true}
        />
        <span>已选时刻：{this.props.time}</span>
      </div>
    );
  }
}
export default compose(
  withApollo,
  graphql(HealthIndexRadar, {
    options: props => {
      return {
        variables: {
          fanId: props.fanId,
          dateTime: props.time
        },
        errorPolicy: 'all',
        fetchPolicy: 'cache-and-network'
      };
    }
  })
)(RadarChart);
