import React, { Component } from 'react';
import { withRouter } from 'react-router-dom';
import { List, Spin } from 'antd';
import Network from 'Components/Network';
import Empty from 'Components/Empty';
// import DataSource from 'Resource/json/fanList.json';
// import DataSource from 'Resource/json/fanList1.json';
import classNames from 'classnames';
import styles from './index.less';
import { compose, graphql, withApollo } from 'react-apollo';
import { AllFan } from 'Api/graphql/deviceList.graphql';
import Url from 'Api';
import { setStore } from 'Api/util';

const cx = classNames.bind(styles);
@withRouter
class ListFormat extends Component {
  getBorderColor = type => {
    return {
      fine: 'green',
      warning: 'orange',
      fault: 'red',
      checking: 'purple',
      deactivated: 'purple'
    }[type];
  };
  go = item => {
    setStore('fanId', item.id);
    setStore('name', item.name);
    setStore('number', `${item.fanNumber} / ${item.motorNumber}`);
    setStore('iconType', item.iconType);
    setStore('parentAreaId', item.area.parent.id);
    setStore('sonAreaId', item.area.id);
    this.props.history.push({
      pathname: '/Detail',
      state: { id: item.id, iconType: item.iconType }
    });
  };
  render() {
    const { data } = this.props;
    if (data.error) return <Network title={'网络错误！'} />;
    if (data.loading)
      return (
        <div className={styles.flexCol}>
          <Spin />
        </div>
      );
    if (!data.allFanAndStatistic.allFan.length) return <Empty />;
    return (
      <div style={{ height: '100%' }}>
        <List
          // className={styles.listContent}
          dataSource={data.allFanAndStatistic.allFan}
          pagination={{
            pageSize: 18,
            showQuickJumper: true,
            hideOnSinglePage: true
          }}
          renderItem={item => (
            <List.Item
              onClick={() => this.go(item)}
              style={{ cursor: 'pointer' }}
            >
              <div className={cx(styles.listWrap, styles[item.status.name])}>
                <div
                  className={styles.fan}
                  style={{
                    background: `url('${Url}${
                      item.imgUrl
                    }') no-repeat center/cover`
                  }}
                />
                <div
                  className={cx(
                    styles.number,
                    styles[this.getBorderColor(item.status.name)]
                  )}
                >
                  <span className={styles.numberText}>
                    设备名称：{item.name}
                  </span>
                </div>
                <div className={styles.cover}>
                  <p>
                    <div>设备编码：</div>
                    <div>
                      {item.fanNumber} / {item.motorNumber}
                    </div>
                  </p>
                  <p>设备名称：{item.name}</p>
                  <p>所属类别：{item.type.displayName}</p>
                  <p>
                    所属区域：{item.area.parent.displayName} -{' '}
                    {item.area.displayName}
                  </p>
                  <p>数据点位总数：{item.checkpointNum}</p>
                  <p>历史预警次数：{item.totalBreakCount}</p>
                </div>
              </div>
            </List.Item>
          )}
        />
      </div>
    );
  }
}
export default compose(
  withApollo,
  graphql(AllFan, {
    options: props => {
      return {
        variables: {
          fanStatusId: props.fanStatusId,
          fanTypeId: props.fanTypeId,
          fanAreaId: props.fanAreaId
        },
        errorPolicy: 'all',
        fetchPolicy: 'cache-and-network'
      };
    }
  })
)(ListFormat);
