import React, { Component } from 'react';
import styles from './index.less';
import List from './list';
import Tip from './Tip';
// import DeviceData from 'Resource/json/device.json';
import { graphql, compose, withApollo } from 'react-apollo';
import {
  AllFanType,
  AllFanStatus,
  AllFatherArea
} from 'Api/graphql/deviceList.graphql';
import Filter from './Filter';

class Device extends Component {
  constructor(props) {
    super(props);
    this.state = {
      fanTypeId: 'all',
      fanStatusId: 'all',
      fanAreaId: 'all',
    };
  }
  //筛选项改变
  onChange = (e, type) => {
    this.setState({ [type]: e.target.value });
  };
  //状态改变
  onChangeStatus = value => {
    this.setState({ fanStatusId:value });
  };
  render() {
    return (
      <div className={styles.container}>
        <div className={styles.top}>
          <Filter onChange={this.onChange} {...this.state} />
          <Tip onChange={this.onChangeStatus} {...this.state} />
        </div>
        <div className={styles.bottom}>
          <List {...this.state} />
        </div>
      </div>
    );
  }
}
export default compose(
  withApollo,
  graphql(AllFanType, {
    options: props => {
      return {};
    },
    name: 'allFanType'
  }),
  graphql(AllFanStatus, {
    options: props => {
      return {};
    },
    name: 'allFanStatus'
  }),
  graphql(AllFatherArea, {
    options: props => {
      return {};
    },
    name: 'allFatherArea'
  })
)(Device);
