import React, { Component } from 'react';
import styles from './index.less';

import List from './list';
import Tree from './tree';
import { Radio } from 'antd';
// import DeviceData from 'Resource/json/device.json';

const RadioGroup = Radio.Group;

export default class SideEdge extends Component {
  constructor(props) {
    super(props);
    this.state = {
      show: true,
      value: 2
    };
  }
  toggle = param => {
    this.props.toggle(param);
    // this.setState({ show: !this.state.show });
  };
  change = e => {
    this.setState({ value: e.target.value });
  };
  render() {
    return (
      <div className={styles.containerLeft}>
        <div className={styles.wrap}>
          <RadioGroup defaultValue={this.state.value} onChange={this.change}>
            <Radio value={2}>设备树</Radio>
            <Radio value={1}>所属类别</Radio>
          </RadioGroup>
          {this.props.close ? (
            <span className={styles.close} onClick={() => this.toggle(false)} />
          ) : null}
        </div>
        {this.state.value === 1 ? <List /> : <Tree />}
      </div>
    );
  }
}
