import React from 'react';
import { getStore } from '../../api/util';
import eventObject from 'Configs/eventSignal';

// 此函数接收一个组件...
const withSubscription = () => (WrappedComponent, selectData) => {
  // ...并返回另一个组件...
  return class extends React.Component {
    constructor(props) {
      super(props);
      this.state = {
        id: !getStore('fanId') ? props.location.state.id : getStore('fanId'),
        iconType: !getStore('iconType')
          ? props.location.state.iconType
          : getStore('iconType')
      };
    }

    componentDidMount() {
      // this.formatData();

      // ...负责订阅相关的操作...
      eventObject.changeFan.add(this.changeFan);
    }

    componentWillUnmount() {
      eventObject.changeFan.remove(this.changeFan);
    }
    changeFan = (id, iconType) => {
      this.setState({ id, iconType });
    };
    render() {
      // ... 并使用新数据渲染被包装的组件!
      // 请注意，我们可能还会传递其他属性
      return <WrappedComponent {...this.state} {...this.props} />;
    }
  };
};
export default withSubscription;
