import React, { Component } from 'react';
// import echarts from 'echarts';
import ReactEcharts from 'echarts-for-react';
import { CheckpointChartData } from 'Api/graphql/deviceDetail.graphql';
import { compose, graphql, withApollo } from 'react-apollo';
import Network from '../../../../components/Network';
import { Spin } from 'antd';
import styles from './index.less';
import colors from 'Configs/color';

class LineChart extends Component {
  formatOption = data => {
    return {
      color: colors,
      grid: {
        left: 60,
        right: 20
      },
      xAxis: {
        type: 'category',
        data: data.xAxis || [],
        symbolOffset: ['50%', 0]
      },
      yAxis: {
        type: 'value',
        name: data.unit
      },
      tooltip: {
        show: true,
        trigger: 'axis'
      },
      legend: {
        data: this.getLegend(data.data),
        type: 'scroll',
        pageTextStyle: {
          color: '#fff'
        },
        pageIconColor: '#fff',
        pageIconSize: 10
      },
      // dataZoom: [
      //   {
      //     type: 'slider',
      //     show: true,
      //     textStyle: {
      //       color: '#fff'
      //     },
      //     dataBackground: {
      //       lineStyle: {
      //         color: '#fff'
      //       },
      //       areaStyle: { color: 'rgba(255,255,255,1)' }
      //     }
      //   },
      //   {
      //     type: 'inside'
      //   }
      // ],
      series: this.getSeries(data.data)
    };
  };
  getSeries = data => {
    let arr = [];
    if (data.length) {
      data.map(value =>
        arr.push({
          data: value.data,
          type: 'line',
          // smooth: true,
          name: value.name
        })
      );
    }
    return arr;
  };
  getLegend = data => {
    let arr = [];
    if (data.length) {
      data.map(value =>
        arr.push({
          name: value.name
        })
      );
    }
    return arr;
  };
  render() {
    const { data } = this.props;
    if (data.error)
      return (
        <div className={styles.flex}>
          <Network title={'网络错误'} />
        </div>
      );
    if (data.loading)
      return (
        <div className={styles.flex}>
          <Spin />
        </div>
      );
    const { checkpointChartData } = data;
    let option = this.formatOption(checkpointChartData);
    return (
      <ReactEcharts
        option={option}
        style={{ width: '100%', height: '500px' }}
        theme="my_theme"
        notMerge={true}
      />
    );
  }
}
export default compose(
  withApollo,
  graphql(CheckpointChartData, {
    options: props => {
      return {
        variables: {
          fanId: props.fanId,
          paramTypeId: props.paramTypeId,
          dateType: props.dateType
        },
        fetchPolicy: 'cache-and-network'
      };
    }
  })
)(LineChart);
