import React, { Component } from 'react';
import styles from './index.less';
import { Timeline, Spin } from 'antd';
// import dataSource from 'Resource/json/history.json';
import { compose, graphql, withApollo } from 'react-apollo';
import { AssessmentHistory } from 'Api/graphql/evaluation.graphql';
import Network from 'Components/Network';
import Empty from 'Components/Empty';

class HistoryRecord extends Component {
  getColor = type => {
    return {
      fault: '#F2637B',
      warning: '#FDB902'
    }[type];
  };
  renderContent = () => {
    const { data } = this.props;
    if (data.error) return <Network />;
    if (data.loading)
      return (
        <div className={styles.flexCol}>
          <Spin />
        </div>
      );
    const { assessmentHistory } = data;
    return (
      <Timeline>
        {assessmentHistory.length ? (
          assessmentHistory.map((value, index) => {
            return (
              <Timeline.Item key={index}>
                <p className={styles.time}>{value.reportTime}</p>
                <div style={{ display: 'flex' }}>
                  <p style={{ width: 200 }}>
                    预警等级：<span
                      style={{ color: this.getColor(value.status) }}
                    >
                      {value.statusDisplayName}
                    </span>
                  </p>
                  <p>预警编号：{value.number}</p>
                </div>
                <div style={{ display: 'flex' }}>
                  <p style={{ width: 200 }}>当前状态：{value.reportStatus}</p>
                  <p>持续时长：{value.duration}</p>
                </div>
                <div style={{ display: 'flex' }}>
                  <p>预警原因：{value.reason}</p>
                </div>
              </Timeline.Item>
            );
          })
        ) : (
          <Empty title={'暂无记录'} style={{ marginTop: 300 }} />
        )}
      </Timeline>
    );
  };
  render() {
    return (
      <div className={styles.panel}>
        <span className={styles.title}>设备预警记录</span>
        <div className={styles.listWrap}>{this.renderContent()}</div>
      </div>
    );
  }
}
export default compose(
  withApollo,
  graphql(AssessmentHistory, {
    options: props => {
      return {
        variables: {
          fanId: props.fanId
        },
        errorPolicy: 'all',
        fetchPolicy: 'cache-and-network',
        notifyOnNetworkStatusChange: true
      };
    }
  })
)(HistoryRecord);
