import React, { Component } from 'react';
import Filter from '../Filter.js';
import TableFormat from './TableFormat.js';
import moment from 'moment';

export default class HistoryRecord extends Component {
  constructor(props) {
    super(props);
    this.state = {
      reportStatus: 'all',
      statusId: 'all',
      reasonId: 'all',
      fanId: 'all',
      sonAreaId:'all',
      parentAreaId:'all',
      startTime: moment()
        .subtract(30, 'day')
        .format('YYYY-MM-DD'),
      endTime: moment().format('YYYY-MM-DD')
    };
  }

  updateParams = obj => {
    this.setState(obj);
  };
  render() {
    return (
      <div style={{ height: '100%' }}>
        <Filter value={this.props.value} updateParams={this.updateParams} />
        <TableFormat {...this.state} />
      </div>
    );
  }
}
