import React, { Component } from 'react';
// import echarts from 'echarts';
import ReactEcharts from 'echarts-for-react';
// import DataSource from 'Resource/json/efficiency.json';

export default class PieChart extends Component {
  formatOption = data => {
    return {
      color: ['#FFAE5C', '#F1677D', '#00FFD6', '#42A4FF', '#9475FF'],
      grid: { left: 60, right: 20 },
      tooltip: { show: true, trigger: 'item' },
      title: { text: data && data.title },
      series: [
        {
          name: '',
          type: 'pie',
          radius: ['55%', '60%'],
          label: {
            normal: {
              show: true,
              formatter: '{b}\n{d}%',
              textStyle: { fontSize: 14, lineHeight: 18 },
              position: 'outside'
            },
            emphasis: { show: true }
          },
          data: data && data.data,
          labelLine: {
            lineStyle: {
              width: 2
            }
          }
        }
      ]
    };
  };
  render() {
    let option = this.formatOption(this.props);
    return (
      <ReactEcharts
        option={option}
        style={{ width: '100%', height: '360px' }}
        theme="my_theme"
      />
    );
  }
}
