import React from 'react';
import { Route, Redirect } from 'react-router-dom';
import Device from 'Containers/Device';
import DeviceDetail from 'Containers/DeviceDetail';
import AlertDetail from 'Containers/AlertDetail';
import Evaluation from 'Containers/Evaluation';
import PointDetails from 'Containers/PointDetails';
import AlertRecord from 'Containers/AlertRecord';
import { getStore } from '../api/util';

const routes = () => (
  <div style={{ height: '100%', width: '100%' }}>
    <Route path="/" component={Device} exact />
    <Route
      path="/Detail"
      render={() =>
        getStore('fanId') ? <DeviceDetail /> : <Redirect to={'/'} />
      }
    />
    <Route
      path="/Evaluation"
      render={() =>
        getStore('fanId') ? <Evaluation /> : <Redirect to={'/'} />
      }
    />
    <Route path="/AlertDetail" component={AlertDetail} />
    <Route
      path="/PointDetails"
      render={() =>
        getStore('fanId') ? <PointDetails /> : <Redirect to={'/'} />
      }
    />
    <Route path="/AlertRecord" component={AlertRecord} />
  </div>
);

export default routes;
