import React, { Component } from 'react';
import Edit from './edit';
import Result from './result.js';
import Empty from './empty.js';

export default class Feedback extends Component {
  constructor(props) {
    super(props);
    this.state = {
      view: null
    };
    this.query = false;
    this.view = null;
  }
  componentDidMount() {
    this.renderContent(this.props);
  }
  componentWillReceiveProps(nextProps) {
    this.renderContent(nextProps);
  }
  renderContent = props => {
    let view = null,
      data = props.data;
    if (data.name === 'checked') {
      view = 'result';
    } else if (data.name === 'finished' && data.isFeedback) {
      view = 'result';
    } else if (data.name === 'finished' && !data.isFeedback) {
      view = 'empty';
    } else if (data.name === 'continuing') {
      view = 'edit';
    }
    this.getView(view, data.name);
    // this.state.view = view
    return view;
  };
  getView = (view, name) => {
    let content = null;
    switch (view) {
      case 'edit':
        content = (
          <Edit
            {...this.props}
            query={this.query}
            view={this.view}
            seeResult={this.seeResult}
            goBack={this.goBack}
            fetchData={this.props.fetchData}
            name={name}
          />
        );
        break;
      case 'result':
        content = <Result {...this.props} modify={this.modify} />;
        break;
      case 'empty':
        content = <Empty write={this.write} />;
        break;
      default:
        content = null;
        break;
    }
    this.setState({ content });
    return content;
  };
  //补填反馈
  write = () => {
    this.query = false;
    this.view = 'empty';
    this.getView('edit');
  };
  //修改反馈
  modify = () => {
    this.query = true;
    this.view = 'result';
    this.getView('edit');
  };
  //查看结果
  seeResult = () => {
    this.getView('result');
    // this.query = true;
  };
  goBack = view => {
    this.getView(view);
  };
  render() {
    return <div>{this.state.content}</div>;
  }
}
