import React, { Component } from 'react';
import styles from './index.less';
import moment from 'moment';
import DropMenu from 'Components/DropMenu';
import { Cascader, Spin } from 'antd';
import { compose, graphql, withApollo } from 'react-apollo';
import { TreeCascadeSelect } from 'Api/graphql/deviceDetail.graphql';
import {
  AlertStatus,
  AlertReason,
  AlertLevel
} from 'Api/graphql/alertRecord.graphql';
import RangePickerFormat from 'Components/RangePickerFormat';

class Filter extends Component {
  constructor(props) {
    super(props);
    this.state = {
      reportStatus: 'all',
      statusId: 'all',
      reasonId: 'all',
      fanId: 'all',
      sonAreaId: 'all',
      parentAreaId: 'all',
      value: ['all', 'all', 'all'],
      startTime: moment()
        .subtract(30, 'day')
        .format('YYYY-MM-DD'),
      endTime: moment().format('YYYY-MM-DD')
    };
  }
  // componentWillUnmount() {
  //   paramsSearch = {};
  // }
  renderCascader = () => {
    const { data } = this.props;
    if (data.error) return <div>网络错误！</div>;
    if (data.loading) return <Spin />;
    return (
      <Cascader
        value={this.state.value}
        options={data.treeCascadeSelect}
        placeholder="Please select"
        allowClear={false}
        onChange={this.onChange}
      />
    );
  };
  //时间改变
  changeDate = (date, dateString, type) => {
    this.setState({ [type]: dateString });
    this.props.updateParams({
      [type]: dateString
    });
  };
  //下拉改变
  changeDropMenu = (e, type) => {
    this.setState({ [type]: e.key });
    this.props.updateParams({ [type]: e.key });
  };
  //级联选择
  onChange = value => {
    this.setState({ value });
    this.props.updateParams({
      value,
      parentAreaId: value[0],
      sonAreaId: value[1],
      fanId: value[2]
    });
  };
  //预警状态
  renderStatus = () => {
    const { alertStatus } = this.props;
    if (alertStatus.error) return <div>网络错误！</div>;
    if (alertStatus.loading) return <Spin />;
    const data = alertStatus.alertReportStatus;
    return (
      <DropMenu
        title={'处理状态'}
        style={{ marginRight: 42 }}
        initValue={this.state.reportStatus}
        callBack={e => this.changeDropMenu(e, 'reportStatus')}
        data={data}
      />
    );
  };
  //预警等级
  renderLevel = () => {
    const { alertLevel } = this.props;
    if (alertLevel.error) return <div>网络错误！</div>;
    if (alertLevel.loading) return <Spin />;
    const data = alertLevel.alertLevelStatus;
    return (
      <DropMenu
        title={'预警等级'}
        style={{ marginRight: 42 }}
        initValue={this.state.statusId}
        callBack={e => this.changeDropMenu(e, 'statusId')}
        data={data}
      />
    );
  };
  //预警原因
  renderReason = () => {
    const { alertReason } = this.props;
    if (alertReason.error) return <div>网络错误！</div>;
    if (alertReason.loading) return <Spin />;
    const data = alertReason.alertReason;
    return (
      <DropMenu
        title={'预警原因'}
        style={{ marginRight: 42 }}
        initValue={this.state.reasonId}
        callBack={e => this.changeDropMenu(e, 'reasonId')}
        data={data}
      />
    );
  };
  render() {
    return (
      <div className={styles.flex} style={{ marginBottom: 24 }}>
        <RangePickerFormat
          title={'时间选择'}
          startTime={this.state.startTime}
          endTime={this.state.endTime}
          changeDate={this.changeDate}
        />
        <div className={styles.flex} style={{ marginRight: 42 }}>
          <span className={styles.text}>设备对象</span>
          {this.renderCascader()}
        </div>
        {this.props.value === 1 ? this.renderLevel() : null}
        {this.props.value === 1 ? this.renderReason() : null}
        {this.props.value === 1 ? this.renderStatus() : null}
      </div>
    );
  }
}
export default compose(
  withApollo,
  graphql(TreeCascadeSelect, {
    options: props => {
      return {};
    }
  }),
  graphql(AlertStatus, {
    options: props => {
      return {};
    },
    name: 'alertStatus'
  }),
  graphql(AlertLevel, {
    options: props => {
      return {};
    },
    name: 'alertLevel'
  }),
  graphql(AlertReason, {
    options: props => {
      return {};
    },
    name: 'alertReason'
  })
)(Filter);
