import React, { Component } from 'react';
import styles from './index.less';
import className from 'classnames';
// import data from 'Resource/json/realTimeData.json';
import { ComponentParamData } from 'Api/graphql/deviceDetail.graphql';
import { compose, graphql, withApollo } from 'react-apollo';
import eventObject from 'Configs/eventSignal';

const cx = className.bind(styles);
class RealTimeData extends Component {
  constructor(props) {
    super(props);
    this.state = {
      componentParamData: []
    };
  }
  componentDidMount() {
    eventObject.mouseEnter.add(this.move);
  }
  componentWillUnmount() {
    eventObject.mouseEnter.remove(this.move);
  }
  move = (id, flag) => {
    this.setState({ componentParamData: [] });
    if (!flag) return;
    this.props.client
      .query({
        query: ComponentParamData,
        variables: {
          fanId: this.props.fanId,
          componentId: id
        },
        fetchPolicy: 'network-only'
      })
      .then(res => {
        const { componentParamData } = res.data;
        this.setState({ componentParamData });
      });
  };
  render() {
    return (
      <div className={cx(this.props.className, styles.wrap)}>
        {/*<span className={styles.triangle} />*/}
        {this.state.componentParamData.map((value, index) => {
          return (
            <div className={styles.item} key={index}>
              <span style={{ width: 150 }}>{value.time}</span>
              <span style={{ width: 140 }}>{value.displayName}</span>
              <span>
                <span style={{ marginRight: 4, fontWeight: 'bold' }}>
                  {value.value}
                </span>
                <span>{value.unit}</span>
              </span>
            </div>
          );
        })}
      </div>
    );
  }
}
export default compose(withApollo)(RealTimeData);
