import React, { Component } from 'react';
import { Table, Spin } from 'antd';
// import DataSource from 'Resource/json/statistics.json';
import { CheckpointTableData } from 'Api/graphql/pointDetails.graphql';
import { compose, graphql, withApollo } from 'react-apollo';
import Network from '../../../../components/Network';
import styles from '../index.less';

class TableFormat extends Component {
  constructor(props) {
    super(props);
    this.state = {
      data: []
    };
    this.columns = [
      {
        title: '预警状态',
        dataIndex: 'status',
        key: 'status'
      },
      {
        title: '预警编号',
        dataIndex: 'number',
        key: 'number'
      },
      {
        title: '预警等级',
        dataIndex: 'level',
        key: 'level',
        render: text => {
          return <span style={{ color: this.getColor(text) }}>{text}</span>;
        }
      },
      {
        title: '发生时间',
        dataIndex: 'createTime',
        key: 'createTime'
      },
      {
        title: '持续时长',
        dataIndex: 'lastTime',
        key: 'lastTime'
      },
      {
        title: '超标量',
        dataIndex: 'overScalar',
        key: 'overScalar'
      },
      {
        title: '预警原因',
        dataIndex: 'reason',
        key: 'reason'
      }
    ];
  }
  getColor = type => {
    return {
      注意: '#FFBA00',
      警告: '#F1677D'
    }[type];
  };
  render() {
    const { data } = this.props;
    if (data.error) return <Network />;
    if (data.loading)
      return (
        <div className={styles.flexCol}>
          <Spin />
        </div>
      );
    return (
      <Table
        columns={this.columns}
        dataSource={data.checkpointTableData}
        bordered={true}
        pagination={{ pageSize: 5, showQuickJumper: true }}
        size={'middle'}
      />
    );
  }
}
export default compose(
  withApollo,
  graphql(CheckpointTableData, {
    options: props => {
      return {
        variables: {
          endTime: props.endTime,
          fanId: props.fanId,
          startTime: props.startTime,
          paramId: props.leftParamId,
          statusId: props.statusId
        },
        errorPolicy: 'all',
        fetchPolicy: 'cache-and-network'
      };
    }
  })
)(TableFormat);
