import React, { Component } from 'react';
import styles from './index.less';
import { Timeline } from 'antd';
import dataSource from 'Resource/json/report.json';

export default class Report extends Component {
  render() {
    return (
      <div className={styles.panel}>
        <span className={styles.title}>离线报告</span>
        <div className={styles.listWrap}>
          <Timeline>
            {dataSource.map((value, index) => {
              return (
                <Timeline.Item key={index}>
                  <p className={styles.time}>{value.time}</p>
                  <div style={{ display: 'flex' }}>
                    <p style={{ width: 230 }}>诊断结果：{value.result}</p>
                    <p>诊断类型：{value.category}</p>
                  </div>
                  <div style={{ display: 'flex' }}>
                    <p>
                      诊断报告：<span className={styles.pdf}>
                        {value.presentation}
                      </span>
                    </p>
                  </div>
                </Timeline.Item>
              );
            })}
          </Timeline>
        </div>
      </div>
    );
  }
}
