import React, { Component } from 'react';
// import ReactEcharts from 'echarts-for-react';
// import DataSource from 'Resource/json/efficiency.json';
import styles from '../index.less';
import Chart from './Option';
import ScatterChart from './ScatterChart.js';
import moment from 'moment';

export default class LineChart extends Component {
  constructor(props) {
    super(props);
    this.state = {
      time: moment().format('YY/MM/DD HH') + ':00'
    };
  }
  //点击事件
  onClick = e => {
    if (e.name && e.componentType === 'series') {
      this.setState({ time: e.name });
    }
  };
  render() {
    return (
      <div className={styles.chartWrap}>
        <Chart onClick={this.onClick} {...this.props} />
        <div className={styles.panel} style={{ width: '40%' }}>
          <ScatterChart time={this.state.time} {...this.props} />
        </div>
      </div>
    );
  }
}
