import React, { Component } from 'react';
import { withRouter } from 'react-router-dom';
import styles from './index.less';
import classNames from 'classnames';
import eventObject from 'Configs/eventSignal';
import ModalContent from './modalContent';
import { Modal, Spin } from 'antd';
import { compose, graphql, withApollo } from 'react-apollo';
import {
  FanCardData,
  CardDurationData
} from 'Api/graphql/deviceDetail.graphql';
const cx = classNames.bind(styles);

@withRouter
class DeviceDetail extends Component {
  constructor(props) {
    super(props);
    this.state = {
      visible: false
    };
    this.machineType = 0;
  }
  componentWillMount() {
    eventObject.scale.add(this.scale);
  }
  componentWillUnmount() {
    eventObject.scale.remove(this.scale);
  }
  go = () => {
    this.props.history.push('/Evaluation');
  };
  scale = params => {
    if (params) {
      this.container.style.display = 'none';
    } else {
      this.container.style.display = 'flex';
    }
  };

  showModal = () => {
    this.setState({
      visible: true
    });
  };

  handleOk = () => {
    eventObject.saveDuration.dispatch();
  };
  updateParams = () => {
    this.setState({
      visible: false
    });
    this.props.cardDurationData.refetch();
  };
  handleCancel = e => {
    this.setState({
      visible: false
    });
  };
  getPic = (type, value) => {
    let pic;
    switch (type) {
      case 'break':
        pic = this.getBreak(value);
        break;
      case 'efficiency':
        pic = this.getEfficiency(value);
        break;
      case 'health':
        pic = this.getHealth(value);
        break;
      default:
        pic = 'blue';
        break;
    }
    return pic;
  };
  //获取预警数
  getBreak = value => {
    let flag;
    if (value === '0') {
      flag = 'blue';
    } else {
      flag = 'warning';
    }
    return flag;
  };
  //获取效率
  getEfficiency = value => {
    let flag;
    if (value < 80 && value >= 60) {
      flag = 'warning';
    } else if (value < 60) {
      flag = 'alert';
    } else {
      flag = 'blue';
    }
    return flag;
  };
  getHealth = value => {
    let flag;
    if (value === '危险') {
      flag = 'alert';
    } else if (value === '警告') {
      flag = 'warning';
    } else {
      flag = 'blue';
    }
    return flag;
  };
  getText = (name, value) => {
    let color;
    switch (name) {
      case 'break':
        color = this.getTextColor(this.getBreak(value));
        break;
      case 'efficiency':
        color = this.getTextColor(this.getEfficiency(value));
        break;
      case 'health':
        color = this.getTextColor(this.getHealth(value));
        break;
      default:
        color = 'blueText';
        break;
    }
    return color;
  };
  getTextColor = type => {
    return {
      blue: 'blueText',
      warning: 'orange',
      alert: 'red'
    }[type];
  };
  renderCard = () => {
    const { fanCardData } = this.props;
    if (fanCardData.error) return <div className={styles.spin}>网络错误！</div>;
    if (fanCardData.loading)
      return (
        <div className={styles.spin}>
          <Spin />
        </div>
      );
    const data = fanCardData.fanCardData;
    return (
      data &&
      data.map((value, index) => {
        return (
          <div className={styles.card} onClick={this.go} key={index}>
            <span
              className={cx(
                styles.triangle,
                styles[this.getPic(value.name, value.value)]
              )}
            />
            <div style={{ display: 'flex', alignItems: 'center' }}>
              <span
                className={
                  styles[
                    `${value.name}_${this.getPic(value.name, value.value)}`
                  ]
                }
                style={{ marginRight: 16 }}
              />
              <div style={{ display: 'flex', flexDirection: 'column' }}>
                <span style={{ fontSize: 16 }}>{value.displayName}</span>
                <span>
                  <span
                    className={cx(
                      styles.text,
                      styles[this.getText(value.name, value.value)]
                    )}
                  >
                    {value.value}
                  </span>
                  {value.unit}
                </span>
              </div>
            </div>
          </div>
        );
      })
    );
  };
  renderDurationData = () => {
    const { cardDurationData } = this.props;
    if (cardDurationData.error) return <div>网络错误！</div>;
    if (cardDurationData.loading) return <Spin />;
    const data = cardDurationData.cardDurationData;
    this.machineType = data.machineType;
    return (
      <div className={styles.card}>
        <span className={styles.edit} onClick={this.showModal} />
        <span className={cx(styles.triangle, styles[this.getPic(data.name)])} />
        <span
          className={cx(styles.smallText, styles[this.getTextColor(data.name)])}
        >
          {data.machineName}
        </span>
        <div style={{ display: 'flex', alignItems: 'center' }}>
          <span className={styles[data.name]} style={{ marginRight: 16 }} />
          <div style={{ display: 'flex', flexDirection: 'column' }}>
            <span style={{ fontSize: 16 }}>{data.displayName}</span>
            <span>
              <span
                className={cx(
                  styles.text,
                  styles[this.getTextColor(data.name)]
                )}
              >
                {data.value}
              </span>
              {data.unit}
            </span>
          </div>
        </div>
      </div>
    );
  };
  render() {
    return (
      <div className={styles.index} ref={ref => (this.container = ref)}>
        {this.renderCard()}
        {this.renderDurationData()}
        <Modal
          title="连续健康运行时长"
          visible={this.state.visible}
          onOk={this.handleOk}
          onCancel={this.handleCancel}
          width={664}
          destroyOnClose={true}
        >
          <ModalContent
            {...this.props}
            updateParams={this.updateParams}
            machineType={this.machineType}
          />
        </Modal>
      </div>
    );
  }
}
export default compose(
  withApollo,
  graphql(FanCardData, {
    options: props => {
      return {
        variables: { fanId: props.fanId },
        errorPolicy: 'all',
        fetchPolicy: 'cache-and-network'
      };
    },
    name: 'fanCardData'
  }),
  graphql(CardDurationData, {
    options: props => {
      return {
        variables: { fanId: props.fanId },
        errorPolicy: 'all',
        fetchPolicy: 'cache-and-network'
      };
    },
    name: 'cardDurationData'
  })
)(DeviceDetail);
