import React, { Component } from 'react';
import styles from './index.less';
import { Button, Spin } from 'antd';
import { compose, graphql, withApollo } from 'react-apollo';
import { FeedbackInfo } from 'Api/graphql/alertDetail.graphql';
import Empty from '../../../components/Empty';

// const data = {
//   verification: '是',
//   repair: '定期维保统一维修/换新',
//   remarks: '本人已核查，预警无误，问题不严重'
// };
class Result extends Component {
  renderContent = () => {
    const { data } = this.props;
    if (data.error) return <Empty title={'网络错误！'} />;
    if (data.loading) return <Spin />;
    const { feedbackInfo } = data;
    return (
      <div className={styles.bg}>
        <div className={styles.flex}>
          <span className={styles.title} style={{ marginBottom: 0 }}>
            核查结论
          </span>
          <span className={styles.line} />
        </div>
        <div className={styles.mb}>
          现场核查结果与预警信息一致：{feedbackInfo.isAgree}
        </div>
        <div className={styles.flex}>
          <span className={styles.title} style={{ marginBottom: 0 }}>
            对策措施
          </span>
          <span className={styles.line} />
        </div>
        <div className={styles.mb}>{feedbackInfo.conclusion}</div>
        <div className={styles.flex}>
          <span className={styles.title} style={{ marginBottom: 0 }}>
            维修结论
          </span>
          <span className={styles.line} />
        </div>
        <div className={styles.mb}>{feedbackInfo.comment}</div>
      </div>
    );
  };
  render() {
    return (
      <div>
        {this.renderContent()}
        <div className={styles.btn}>
          <Button
            type={'primary'}
            ghost
            style={{ width: 120 }}
            onClick={this.props.modify}
          >
            修改反馈
          </Button>
        </div>
      </div>
    );
  }
}
export default compose(
  withApollo,
  graphql(FeedbackInfo, {
    options: props => {
      return {
        variables: { messageId: props.id },
        errorPolicy: 'all',
        fetchPolicy: 'network-only'
      };
    }
  })
)(Result);
