import React, { Component } from 'react';
import styles from './index.less';
import moment from 'moment';
import eventObject from 'Configs/eventSignal';
import { Checkbox, DatePicker, InputNumber, message, Select, Spin } from 'antd';
import Network from 'Components/Network';
import {
  CalculateDuration,
  CalculateStartTime,
  RetrieveHealthDuration,
  UpdateHealthDuration
} from 'Api/graphql/deviceDetail.graphql';
import { compose, graphql, withApollo } from 'react-apollo';

const Option = Select.Option;

class ModalContent extends Component {
  constructor(props) {
    super(props);
    this.state = {
      startTime: moment().format('YYYY-MM-DD HH:mm'),
      duration: null
    };
    this.paramsSearch = {
      fanId: props.fanId,
      machineType: 0,
      timeType: 0,
      isDisplay: false
    };
  }
  componentDidMount() {
    this.formatData(this.props);
    eventObject.saveDuration.add(this.save);
  }
  componentWillReceiveProps(nextProps) {
    this.formatData(nextProps);
  }
  componentWillUnmount() {
    eventObject.saveDuration.remove(this.save);
  }
  formatData = props => {
    const { data } = props;
    if (data.error || data.loading) return null;
    const { retrieveHealthDuration } = data;
    this.setState({
      startTime: retrieveHealthDuration.startTime,
      duration: retrieveHealthDuration.duration
    });
    this.paramsSearch = Object.assign(
      {},
      this.paramsSearch,
      retrieveHealthDuration
    );
  };
  range = (start, end) => {
    const result = [];
    for (let i = start; i < end; i++) {
      result.push(i);
    }
    return result;
  };

  //日期禁止
  disabledDate = current => {
    return current && current > moment().endOf('day');
  };
  disabledDateTime = () => {
    let myDate = new Date();
    return {
      disabledHours: () => this.range(myDate.getHours() + 1, 24),
      disabledMinutes: () => this.range(myDate.getMinutes() + 1, 60)
      // disabledSeconds: () => [55, 56],
    };
  };
  //类型选择
  handleChange = value => {
    this.paramsSearch.machineType = value;
    this.props.data.refetch({ machineType: value });
  };
  //日期选择
  onOk = value => {
    this.paramsSearch.startTime = value.format('YYYY-MM-DD HH:mm');
    this.setState({ startTime: value.format('YYYY-MM-DD HH:mm') });
    this.props.client
      .query({
        query: CalculateDuration,
        variables: {
          startTime: this.paramsSearch.startTime,
          timeType: this.paramsSearch.timeType
        }
      })
      .then(res => {
        let result = res.data.calculateDuration.duration;
        this.setState({
          duration: result
        });
      });
  };
  //连续时长改变
  changeDurationTime = value => {
    this.setState({ duration: value });
  };
  fetchTime = () => {
    if (!this.state.duration) return;
    this.props.client
      .query({
        query: CalculateStartTime,
        variables: {
          duration: this.state.duration,
          timeType: this.paramsSearch.timeType
        }
      })
      .then(res => {
        let result = res.data.calculateStartTime.startTime;
        this.setState({
          startTime: result
        });
      });
  };
  /*//按天按小时
  onChangeTimeType = e => {
    const { value } = e.target;
    this.paramsSearch.timeType = value;
    let duration = this.state.duration;
    if (value === 0) {
      duration = (duration * 24).toFixed(2);
    } else {
      duration = (duration / 24).toFixed(2);
    }
    this.setState({ duration: duration });
  };*/
  //是否自动展示
  onChange = e => {
    this.paramsSearch.isDisplay = e.target.checked;
  };
  //save
  save = () => {
    if (!this.state.duration) {
      message.warning('时长不能为空');
      return;
    }
    let variables = Object.assign({}, this.paramsSearch, this.state);
    this.props
      .updateHealthDuration({ variables: { inputData: variables } })
      .then(res => {
        if (res.data.updateHealthDuration) {
          message.success('提交成功');
          this.props.updateParams();
        } else {
          message.error('提交失败');
        }
      });
  };
  render() {
    const { data } = this.props;
    if (data.error) return <Network title={'网络错误'} />;
    if (data.loading) return <Spin />;
    const { retrieveHealthDuration } = data;
    return (
      <div className={styles.wrap}>
        <div className={styles.item}>
          <span>选择部件</span>
          <Select
            defaultValue={retrieveHealthDuration.machineType}
            style={{ width: '87%' }}
            onChange={this.handleChange}
          >
            <Option value={0}>风机</Option>
            <Option value={1}>电机</Option>
          </Select>
        </div>
        <div className={styles.item}>
          <div>
            <span style={{ marginRight: 5 }}>起始时刻</span>
            <DatePicker
              format={'YYYY-MM-DD HH:mm'}
              placeholder="Select Time"
              value={moment(this.state.startTime, 'YYYY-MM-DD HH:mm')}
              onChange={this.onOk}
              showTime={true}
              disabledDate={this.disabledDate}
              disabledTime={this.disabledDateTime}
              allowClear={false}
            />
          </div>
          <span>当前时刻：{moment().format('YYYY-MM-DD HH:mm')}</span>
        </div>
        <div className={styles.item}>
          <span>连续健康运行时长</span>
          <InputNumber
            style={{ width: '70%' }}
            onChange={this.changeDurationTime}
            value={this.state.duration}
            onBlur={this.fetchTime}
          />
          天
        </div>
        <div className={styles.item}>
          <Checkbox
            onChange={this.onChange}
            defaultChecked={retrieveHealthDuration.isDisplay}
          >
            自动展示本部件的连续健康运行时长
          </Checkbox>
        </div>
      </div>
    );
  }
}
export default compose(
  withApollo,
  graphql(RetrieveHealthDuration, {
    options: props => {
      return {
        variables: { fanId: props.fanId, machineType: props.machineType },
        fetchPolicy: 'cache-and-network'
      };
    }
  }),
  graphql(UpdateHealthDuration, {
    name: 'updateHealthDuration'
  })
)(ModalContent);
