import React, { Component } from 'react';
import styles from './index.less';
import { Spin } from 'antd';
import className from 'classnames';
// import DeviceData from 'Resource/json/device.json';
import { graphql, compose, withApollo } from 'react-apollo';
import { BreakStatistic } from 'Api/graphql/deviceList.graphql';

const cx = className.bind(styles);
class Tip extends Component {
  render() {
    const { data } = this.props;
    if (data.error) return <div>网络错误！</div>;
    if (data.loading) return <Spin />;
    const { statistic } = data.allFanAndStatistic;

    return (
      <div className={styles.container}>
        {statistic.map((value, index) => {
          return (
            <div
              className={styles.flex}
              key={index}
              onClick={() => this.props.onChange(value.id)}
            >
              <span className={styles[value.level]} />
              <span className={styles.middle}>
                <span
                  className={cx(
                    styles.count,
                    styles[
                      this.props.fanStatusId === value.id ? 'countSelect' : ''
                    ]
                  )}
                >
                  {value.count}
                </span>
                <span
                  className={cx(
                    styles.title,
                    styles[
                      this.props.fanStatusId === value.id ? 'titleSelect' : ''
                    ]
                  )}
                >
                  {value.title}
                </span>
              </span>
              <span style={{ marginTop: -5 }}>{value.unit}</span>
            </div>
          );
        })}
      </div>
    );
  }
}
export default compose(
  withApollo,
  graphql(BreakStatistic, {
    options: props => {
      return {
        variables: {
          fanStatusId: 'all',
          fanTypeId: 'all',
          fanAreaId: 'all'
        },
        errorPolicy: 'all',
        fetchPolicy: 'cache-and-network'
      };
    }
  })
)(Tip);
