import React, { Component } from 'react';
import styles from './index.less';
// import DataSource from 'Resource/json/statisticsChart.json';
import { compose, graphql, withApollo } from 'react-apollo';
import { AlertStatisticIndicator } from 'Api/graphql/alertRecord.graphql';
import Network from 'Components/Network';
import { Spin } from 'antd';

class Tip extends Component {
  render() {
    const { data } = this.props;
    if (data.error) return <Network title={'网络错误'} />;
    if (data.loading) return <Spin />;
    const { alertStatisticIndicator } = data;
    return (
      <div className={styles.wrap}>
        {alertStatisticIndicator.map((value, index) => {
          return (
            <div key={index} className={styles.tip}>
              <div>
                <div>{value.displayName}</div>
                <span className={styles.number}>{value.number}</span>
                {value.unit}
              </div>
              <div className={styles.line} />
            </div>
          );
        })}
      </div>
    );
  }
}
export default compose(
  withApollo,
  graphql(AlertStatisticIndicator, {
    options: props => {
      return {
        variables: {
          fanId: props.fanId,
          startTime: props.startTime,
          endTime: props.endTime,
          parentAreaId: props.parentAreaId,
          sonAreaId: props.sonAreaId
        },
        errorPolicy: 'all',
        fetchPolicy: 'cache-and-network'
      };
    }
  })
)(Tip);
