import React, { Component } from 'react';
import * as THREE from 'three';
import styles from './index.less';
import TrackballControls from 'three-trackballcontrols';
import eventObject from 'Configs/eventSignal';
const STLLoader = require('three-stl-loader')(THREE);

export default class Test extends Component {
  constructor(props) {
    super(props);
    this.state = {
      loading: true
    };
    this.diagnosis_fan_ubf = null;
    this.renderer = null;
    this.scene = new THREE.Scene(); //场景构建
    this.camera = null;
    this.trackballControls = null;
    this.color = '#fff';
    this.obj = this.props.obj;
  }
  componentDidMount() {
    this.initData();
    window.addEventListener('resize', () => this.resize());
    eventObject.mouse.add(this.handleHover);
    eventObject.scale.add(this.resize);
    eventObject.changeModal.add(this.changeModal);
  }
  componentWillUnmount() {
    window.removeEventListener('resize', () => this.resize());
    eventObject.mouse.remove(this.handleHover);
    eventObject.scale.remove(this.resize);
    eventObject.changeModal.remove(this.changeModal);
    this.clearScene();
  }
  componentWillReceiveProps(nextProps) {
    nextProps.obj.map(value => {
      if (this[value.name]) {
        this[value.name].material.color.set(this.getColor(value.status));
      }
    });
  }
  changeModal = obj => {
    this.clearScene();
    this.init(obj);
    this.reset();
    this.renderScene();
  };
  clearScene = () => {
    this.animate && cancelAnimationFrame(this.animate);
    this.deleteGroup(this.group);
  };
  //卸载模型
  deleteGroup = group => {
    if (!group) return;
    // 删除掉所有的模型组内的mesh
    group.children.map(item => {
      if (item instanceof THREE.Mesh) {
        item.geometry.dispose(); // 删除几何体
        item.material.dispose(); // 删除材质
        this[item.name] = null;
      }
    });
    this.scene.remove(this.group);
  };
  //尺寸变化
  resize = () => {
    if (this.fan) {
      this.renderer.setSize(this.fan.offsetWidth, this.fan.offsetHeight);
    }
  };
  //选中效果
  handleHover = props => {
    // console.log(props.obj)
    if (props.current && this[props.current]) {
      if (props.move) {
        this[props.current].material.color.set('#00FFD6', 0.5);
      } else {
        this.obj.map(value => {
          if (value.name === props.current) {
            this[props.current].material.color.set(this.getColor(value.status));
          }
        });
      }
    }
    this.resize();
  };
  //初始化数据
  initData = () => {
    this.initCamera();
    this.initRender();
    this.init(this.obj);
    this.renderTrackballControls();
    this.renderSpotLight();
    this.renderScene();
  };
  //相机构建
  initCamera = () => {
    this.camera = new THREE.PerspectiveCamera(
      35,
      window.innerWidth / window.innerHeight,
      1,
      15
    );
    //第一个参数改角度   第三个参数更改默认进来的显示比例
    this.camera.position.set(-1.3, 1, 3);
    this.camera.lookAt(0, 0, 0);
  };
  //渲染器构建
  initRender = () => {
    this.renderer = new THREE.WebGLRenderer({ antialias: true, alpha: true }); //渲染器构建
    this.renderer.setClearAlpha(0);
    this.renderer.setSize(this.fan.offsetWidth, this.fan.offsetHeight); //window.innerWidth, window.innerHeight
    this.renderer.shadowMap.enabled = true; //激活阴影
  };
  //模型加载
  init = obj => {
    //构建一个坐标轴
    // const axes = new THREE.AxisHelper(10);
    // this.scene.add(axes);
    const stlLoader = new STLLoader();
    this.group = new THREE.Group();
    //风机箱
    obj.map(value => {
      let material = new THREE.MeshPhongMaterial({
        color: this.getColor(value.status)
      });
      return stlLoader.load(value.url, geometry => {
        let mesh = new THREE.Mesh(geometry, material);
        this[value.name] = this.formatData(
          mesh,
          geometry,
          value.position,
          value.name
        );
        this.group.add(this[value.name]);
      });
    });
    this.scene.add(this.group);
    this.fan.appendChild(this.renderer.domElement);
  };
  formatData = (mesh, geometry, position, name) => {
    mesh.scale.set(0.0003, 0.0003, 0.0003);
    mesh.position.x = position.x;
    mesh.position.y = position.y;
    mesh.position.z = position.z;
    mesh.castShadow = true;
    mesh.receiveShadow = true;
    mesh.name = name;
    geometry.center();
    return mesh;
  };
  renderTrackballControls = () => {
    this.trackballControls = new TrackballControls(
      this.camera,
      this.renderer.domElement
    );
    this.trackballControls.rotateSpeed = 3.0;
    this.trackballControls.zoomSpeed = 2.0;
    this.trackballControls.panSpeed = 1.0;
    this.trackballControls.noPan = true;
    this.trackballControls.minDistance = 1.9;
    this.trackballControls.maxDistance = 14.5;
    this.trackballControls.dynamicDampingFactor = 0.3;
  };
  //添加材质灯光阴影
  renderSpotLight = () => {
    let light = new THREE.HemisphereLight('#ccdcdf', '#2846e4', 1);
    light.position.set(10, 150, 100);
    this.scene.add(light);
  };
  renderScene = () => {
    this.animate = requestAnimationFrame(this.renderScene);
    var clock = new THREE.Clock();
    var delta = clock.getDelta();
    this.trackballControls.update(delta);
    if (
      this.diagnosis_fan_ubf &&
      this.diagnosis_bearing_FN &&
      this.diagnosis_cpl_misaf &&
      this.box
    ) {
      eventObject.loading.dispatch(false);
      if (this.props.rotate !== 'stop') {
        this.diagnosis_fan_ubf.rotation.x -= 0.02;
        this.diagnosis_cpl_misaf.rotation.x -= 0.02;
      }
      this.renderer.render(this.scene, this.camera);
    }
  };
  ////创建控件并绑定在相机上
  getColor = type => {
    let color;
    switch (type) {
      case 'fine':
        color = '#ffffff';
        break;
      case 'fault':
        color = '#DE7070';
        break;
      case 'warning':
        color = '#FFE676';
        break;
      default:
        color = '#ffffff';
        break;
    }
    return color;
  };
  reset = () => {
    this.initCamera();
    this.renderTrackballControls();
  };
  render() {
    return (
      <div className={styles.wrap}>
        <div className={styles.status}>
          状态：{this.props.rotate === 'stop' ? '停机' : '运行'}
        </div>
        <button onClick={this.reset} className={styles.reset}>
          <span className={styles.icon} />一键复位
        </button>
        <div
          ref={ref => (this.fan = ref)}
          style={{ width: '100%', height: '100%' }}
        />
      </div>
    );
  }
}
