import React, { Component } from 'react';
import styles from './index.less';
import { message, Icon } from 'antd';
import moment from 'moment';
import Empty from 'Components/Empty';
import RangePickerFormat from 'Components/RangePickerFormat';
import Chart from './Chart';
import { compose, graphql, withApollo } from 'react-apollo';
import { StatisticAndOtherCheckpointTree } from 'Api/graphql/pointDetails.graphql';
import RadioFormat from 'Components/Radio';
import Filter from './filter';
import { removeStore } from '../../../../api/util';

class Trend extends Component {
  constructor(props) {
    super(props);
    this.state = {
      checked: false,
      otherPoint: { key: 'only' },
      value: [],
      startTime: moment()
        .subtract(6, 'day')
        .format('YYYY-MM-DD'),
      endTime: moment().format('YYYY-MM-DD'),
      rightParamId: [],
      dataFreq: '15min',
      freq: 'week',
      show: false,
      select: false,
      checkValue: {}
    };
    this.data = [
      { id: '5min', displayName: '5min' },
      { id: '10min', displayName: '10min' },
      { id: '15min', displayName: '15min' }
    ];
    this.freq = [
      { id: 'date', displayName: '当日' },
      { id: 'week', displayName: '7日' },
      { id: 'month', displayName: '30日' },
      { id: 'custom', displayName: '自定义' }
    ];
  }
  componentDidMount() {
    removeStore('paramsSearch');
  }
  componentWillUnmount() {
    removeStore('paramsSearch');
  }
  componentWillReceiveProps(nextProps, nextContext) {
    if (this.props.leftParamId !== nextProps.leftParamId) {
      this.setState({ rightParamId: [] });
      removeStore('paramsSearch');
    }
  }

  //测点选择
  onCheck = (type, value) => {
    this.setState({ [type]: value });
  };
  //时间选择
  changeDate = (date, dateString, type) => {
    this.setState({ [type]: dateString });
  };
  //频率切换
  changeRadio = e => {
    if (e.target.value !== '15min') {
      message.warning('所选时段内数据量较大，请耐心等待。');
    }
    this.setState({ dataFreq: e.target.value });
  };
  //时间选择切换
  changeDatePicker = e => {
    let value = e.target.value;
    let startTime;
    switch (value) {
      case 'date': //当日
        startTime = moment().format('YYYY-MM-DD');
        this.setState({ show: false });
        break;
      case 'week': //7日
        startTime = moment()
          .subtract(6, 'day')
          .format('YYYY-MM-DD');
        this.setState({ show: false });
        break;
      case 'month': //30日
        startTime = moment()
          .subtract(29, 'day')
          .format('YYYY-MM-DD');
        this.setState({ show: false });
        break;
      case 'custom': //自定义
        startTime = moment()
          .subtract(6, 'day')
          .format('YYYY-MM-DD');
        this.setState({ show: true });
        break;
      default:
        break;
    }
    this.setState({ startTime, freq: value });
  };
  toggleSelect = () => {
    this.setState({ select: !this.state.select });
  };
  //更新参数
  updateParams = params => {
    console.log(params);
    let arr = [];
    for (let i in params) {
      let obj = { paramTypeId: '', paramIds: [] };
      if (params[i].length) {
        obj.paramTypeId = i;
        obj.paramIds = params[i];
        arr.push(obj);
      }
    }
    this.setState({ rightParamId: arr });
    this.toggleSelect();
  };
  render() {
    return (
      <div className={styles.container}>
        <div className={styles.top}>
          <div className={styles.flex}>
            <span className={styles.text}>选择时间</span>
            <RadioFormat
              data={this.freq}
              change={this.changeDatePicker}
              defaultValue={this.state.freq}
            />
            <RangePickerFormat
              style={{
                display: this.state.show ? 'block' : 'none',
                marginRight: 0
              }}
              startTime={this.state.startTime}
              endTime={this.state.endTime}
              changeDate={this.changeDate}
            />
          </div>
          <div className={styles.flex}>
            <span className={styles.text}>选择频率</span>
            <RadioFormat
              data={this.data}
              change={this.changeRadio}
              defaultValue={this.state.dataFreq}
            />
          </div>
          <button className={styles.btn} onClick={this.toggleSelect}>
            与其他测点对比查看
            <Icon
              type={this.state.select ? 'up' : 'down'}
              style={{ marginLeft: 4 }}
            />
          </button>
        </div>
        {this.state.select && (
          <Filter
            {...this.props}
            onClick={this.toggleSelect}
            onCheck={this.onCheck}
            updateParams={this.updateParams}
          />
        )}
        {this.props.leftParamId ? (
          <Chart {...this.props} {...this.state} />
        ) : (
          <Empty />
        )}
      </div>
    );
  }
}
export default compose(
  withApollo,
  graphql(StatisticAndOtherCheckpointTree, {
    options: props => {
      return {
        variables: {
          fanId: props.fanId
        },
        errorPolicy: 'all',
        fetchPolicy: 'cache-and-network'
      };
    }
  })
)(Trend);
