import React, { Component } from 'react';
import styles from './index.less';
import SideEdge from 'Components/SideEdge';
import ThreeLevelMenu from 'Components/ThreeLevelMenu';
import Content from './Content';
import eventObject from 'Configs/eventSignal';
import withSubscription from 'Components/WithSubscription';
import { Spin } from 'antd';

@withSubscription()
class DeviceDetail extends Component {
  constructor(props) {
    super(props);
    this.state = {
      show: true,
      value: 1,
      loading: true
    };
  }
  componentWillMount() {
    //放大缩小
    eventObject.scale.add(params => this.toggle(!params));
    //loading效果
    eventObject.loading.add(this.update);
    //模型切换
    eventObject.changeFan.add(this.changeFan);
  }
  componentWillUnmount() {
    eventObject.scale.remove(params => this.toggle(!params));
    eventObject.loading.remove(this.update);
    eventObject.changeFan.remove(this.changeFan);
  }
  toggle = param => {
    this.setState({ show: param });
  };
  update = param => {
    if (!this.state.loading) return;
    this.setState({ loading: param });
  };
  changeFan = () => {
    this.setState({ loading: true });
  };
  render() {
    return (
      <div style={{ height: '100%' }}>
        <Spin
          wrapperClassName={styles.spin}
          spinning={this.state.loading}
          tip={'本设备3D模型文件较大，首次加载时间约为10秒，请耐心等待'}
        >
          <ThreeLevelMenu current={'Detail'} />
          <div className={styles.container}>
            {this.state.show ? (
              <SideEdge toggle={this.toggle} close={true} />
            ) : (
              <span className={styles.open} onClick={() => this.toggle(true)} />
            )}
            <Content
              style={{
                width: !this.state.show ? '100%' : 'calc(100% - 308px) '
              }}
              fanId={this.props.id}
              iconType={this.props.iconType}
            />
          </div>
        </Spin>
      </div>
    );
  }
}
export default DeviceDetail;
