import React, { Component } from 'react';
import CheckBoxFormat from 'Components/CheckBoxFormat';
// import PointFilter from 'Resource/json/pointFilter.json';
import styles from './index.less';
import { Button } from 'antd';
import { AllFilterParams } from 'Api/graphql/pointDetails.graphql';
import { compose, graphql, withApollo } from 'react-apollo';
import Network from 'Components/Network';
import { Spin } from 'antd';
import { getStore, setStore } from 'Api/util';

class Filter extends Component {
  constructor(props) {
    super(props);
    this.state = getStore('paramsSearch')
      ? JSON.parse(getStore('paramsSearch'))
      : {};
    this.paramsSearch = {};
  }
  onChange = (value, type) => {
    this.props.onCheck(type, value);
    this.setState({ [type]: value });
  };
  //保存
  save = () => {
    this.paramsSearch = this.state;
    setStore('paramsSearch', this.paramsSearch);
    this.props.updateParams(this.paramsSearch);
  };
  render() {
    const { data } = this.props;
    if (data.error) return <Network />;
    if (data.loading) return <Spin />;
    return (
      <div className={styles.filter}>
        {data.allFilterParams.map((value, index) => {
          return (
            <div key={index} className={styles.filterItem}>
              <span className={styles.name}>{value.displayName}</span>
              <CheckBoxFormat
                style={{ width: 'calc(100% - 100px)' }}
                paramId={this.props.leftParamId}
                data={value.children}
                onChange={e => this.onChange(e, value.id)}
                value={this.state[value.id]}
              />
            </div>
          );
        })}
        <div className={styles.btnWrap}>
          <Button
            ghost
            style={{ marginRight: 14, width: 120 }}
            type="primary"
            onClick={this.save}
          >
            保存
          </Button>
          <Button
            ghost
            type="primary"
            style={{ width: 120 }}
            onClick={this.props.onClick}
          >
            取消
          </Button>
        </div>
      </div>
    );
  }
}
export default compose(
  withApollo,
  graphql(AllFilterParams, {
    options: props => {
      return {
        variables: {
          fanId: props.fanId
        },
        errorPolicy: 'all'
      };
    }
  })
)(Filter);
