import React, { Component } from 'react';
import styles from '../index.less';
import Filter from './Filter.js';
import TableFormat from './TableFormat.js';
import Chart from './chart';
import moment from 'moment';
import Empty from '../../../../components/Empty';

export default class Statistics extends Component {
  constructor(props) {
    super(props);
    this.state = {
      statusId: 'all',
      startTime: moment()
        .subtract(1, 'month')
        .format('YYYY-MM-DD'),
      endTime: moment().format('YYYY-MM-DD'),
      fanId: props.fanId
    };
  }
  updateParams = obj => {
    this.setState(obj);
  };
  render() {
    return (
      <div className={styles.container}>
        <Filter updateParams={this.updateParams} />
        <div>
          <div className={styles.flex} style={{ marginBottom: 20 }}>
            <span className={styles.title}>历史记录</span>
          </div>
          {this.props.leftParamId ? (
            <TableFormat {...this.state} {...this.props} />
          ) : (
            <Empty title={'无异常记录'} />
          )}
        </div>
        <div>
          <div className={styles.flex} style={{ marginTop: 16 }}>
            <span className={styles.title}>趋势汇总</span>
          </div>
          {this.props.leftParamId ? (
            <Chart {...this.state} {...this.props} />
          ) : (
            <Empty />
          )}
        </div>
      </div>
    );
  }
}
