import React, { Component } from 'react';
import styles from './index.less';
import { Radio } from 'antd';
import PropTypes from 'prop-types';
const RadioButton = Radio.Button;
const RadioGroup = Radio.Group;
//传过来的props需要以这种形式
// const arr = [
//   { key: "all", display_name: "总体" },
//   { key: "business", display_name: "商业" }
// ];
export default class radioButton extends Component {
  static propTypes = {
    data: PropTypes.array.isRequired,
    defaultValue: PropTypes.string,
    radioStyle: PropTypes.string,
    style: PropTypes.any
  };
  //切换时的回调
  change = e => {
    this.props.change && this.props.change(e);
  };
  render() {
    let { data, style, defaultValue, radioStyle, disabled } = this.props;
    //如果有radioStyle 样式 则不作用radio样式 ,大部分按钮是radio样式
    return (
      <div className={styles[radioStyle] || styles.radio} style={style}>
        <RadioGroup
          value={defaultValue}
          onChange={this.change}
          disabled={disabled}
        >
          {data.map((value, index) => {
            return (
              <RadioButton
                value={value.id}
                key={index}
                disabled={value.disabled}
              >
                {value.displayName}
              </RadioButton>
            );
          })}
        </RadioGroup>
      </div>
    );
  }
}
