import React, { Component } from 'react';
import styles from '../index.less';
import ReactEcharts from 'echarts-for-react';
// import DataSource from 'Resource/json/statisticsChart.json';
import { compose, graphql, withApollo } from 'react-apollo';
import { CheckpointCountChartData } from 'Api/graphql/pointDetails.graphql';
import Network from '../../../../components/Network';
import { Spin } from 'antd';

class LineChart extends Component {
  formatOption = data => {
    return {
      color: {
        type: 'linear',
        x: 0,
        y: 0,
        x2: 1,
        y2: 0,
        colorStops: [
          {
            offset: 0,
            color: '#FFC200' // 0% 处的颜色
          },
          {
            offset: 1,
            color: '#FE4719' // 100% 处的颜色
          }
        ],
        globalCoord: false // 缺省为 false
      },
      grid: {
        left: 60,
        right: 20
      },
      xAxis: {
        type: 'category',
        data: data.xAxis || [],
        symbolOffset: ['50%', 0]
      },
      yAxis: {
        type: 'value',
        name: data.unit,
        minInterval: 1
      },
      tooltip: {
        show: true,
        trigger: 'axis'
      },
      series: [
        {
          data: data.data,
          type: 'line',
          symbol: 'none',
          name: ''
        }
      ]
    };
  };
  render() {
    const { data } = this.props;
    if (data.error) return <Network />;
    if (data.loading)
      return (
        <div className={styles.flexCol}>
          <Spin />
        </div>
      );
    const { checkpointCountChartData } = data;
    let option = this.formatOption(checkpointCountChartData.chartData);
    return (
      <div style={{ display: 'flex' }}>
        <ReactEcharts
          option={option}
          style={{ width: '85%', height: '315px' }}
          theme="my_theme"
        />
        <div className={styles.tip}>
          <div>
            <div>异常总数</div>
            <span className={styles.number}>
              {checkpointCountChartData.troubleCount}
            </span>次
          </div>
          <div className={styles.line} />
          <div>
            <div>异常时长占比</div>
            <span className={styles.number}>
              {checkpointCountChartData.proportion}
            </span>%
          </div>
        </div>
      </div>
    );
  }
}
export default compose(
  withApollo,
  graphql(CheckpointCountChartData, {
    options: props => {
      return {
        variables: {
          endTime: props.endTime,
          fanId: props.fanId,
          startTime: props.startTime,
          paramId: props.leftParamId,
          statusId: props.statusId
        },
        errorPolicy: 'all',
        fetchPolicy: 'cache-and-network'
      };
    }
  })
)(LineChart);
