import React, { Component } from 'react';
import ReactEcharts from 'echarts-for-react';
import Network from '../Network'
import {Spin} from 'antd'
export default class LineChart extends Component {
  formatOption = data => {
    return {
      color: ['#00FFD6', '#FFAE5C'],
      grid: {
        left: 60,
        right: 160
      },
      legend: { show: true },
      xAxis: [
        {
          type: 'category',
          data: data.xAxis || [],
          symbolOffset: ['50%', 0]
        },
        {
          axisLine: {
            show: false
          }
        }
      ],
      // yAxis: data.data.length
      //   ? this.getUnit(data.data)
      //   : [
      //       {
      //         type: 'value'
      //       }
      //     ],
      yAxis: [
        {
          type: 'value'
        }
      ],
      tooltip: {
        show: true,
        trigger: 'axis'
      },
      dataZoom: [
        {
          type: 'slider',
          show: true,
          textStyle: {
            color: '#fff'
          },
          dataBackground: {
            lineStyle: {
              color: '#fff'
            },
            areaStyle: { color: 'rgba(255,255,255,1)' }
          }
        },
        {
          type: 'inside'
        }
      ],
      // series: data.data.length ? this.getSeriesData(data.data) : []
      series: []
    };
  };
  getSeriesData = data => {
    let arr = [];
    data.map((value, index) => {
      return arr.push({
        data: value.data,
        type: 'line',
        // symbol: 'none',
        smooth: true,
        name: value.name,
        yAxisIndex: index
      });
    });
    return arr;
  };
  getUnit = data => {
    let arr = [];
    data.map((value, index) => {
      let obj = {
        type: 'value',
        name: value.unit
      };
      if (index > 0) {
        obj.nameTextStyle = {
          padding: [0, -25, 0, 0]
        };
      }
      arr.push(obj);
    });
    return arr;
  };
  render() {
    const { data } = this.props;
    if (data.error) return <Network />;
    if (data.loading) return <Spin />;
    let option = this.formatOption(data.checkpointCompareChartData);
    return (
      <ReactEcharts
        option={option}
        style={{ width: '100%', height: '720px' }}
        theme="my_theme"
        notMerge={true}
      />
    );
  }
}

