import React, { Component } from 'react';
import styles from './index.less';
import moment from 'moment';
import { DatePicker } from 'antd';

export default class RangePickerFormat extends Component {
  render() {
    const { startTime, endTime, changeDate, style, title } = this.props;
    return (
      <div className={styles.flex} style={style}>
        <span className={styles.text}>{title}</span>
        <DatePicker
          onChange={(date, dateString) =>
            changeDate(date, dateString, 'startTime')
          }
          value={moment(startTime)}
          format={'YYYY-MM-DD'}
          allowClear={false}
          style={{ marginRight: 8 }}
        />~
        <DatePicker
          style={{ marginLeft: 8 }}
          onChange={(date, dateString) =>
            changeDate(date, dateString, 'endTime')
          }
          value={moment(endTime)}
          format={'YYYY-MM-DD'}
          allowClear={false}
        />
      </div>
    );
  }
}
