import React, { Component } from 'react';
import styles from './index.less';
import Card from './Card';
import Health from './Health';
import Trend from './Trend';

export default class DeviceDetailContent extends Component {
  toggle = params => {
    if (params) {
      this.container.style.height = '100%';
    } else {
      this.container.style.height = 'calc(100% - 160px)';
    }
  };
  render() {
    return (
      <div className={styles.containerRight} style={this.props.style}>
        <Card {...this.props} />
        <div className={styles.flex} ref={ref => (this.container = ref)}>
          <Health toggle={this.toggle} {...this.props} />
          <Trend {...this.props}/>
        </div>
      </div>
    );
  }
}
