import React, { Component } from 'react';
import styles from './index.less';
import { Spin, Tree } from 'antd';
// import TreeData from 'Resource/json/tree.json';
// import DeviceData from 'Resource/json/device.json';
import { compose, graphql, withApollo } from 'react-apollo';
import { TreeCascadeSelect, TreeFan } from 'Api/graphql/deviceDetail.graphql';
import Network from 'Components/Network';
import Filter from './filter';
import { getStore, setStore } from '../../api/util';
import eventObject from 'Configs/eventSignal';
import classNames from 'classnames';

const { TreeNode } = Tree;
const cx = classNames.bind(styles);
let paramsSearch = {
  parentAreaId: 'all',
  sonAreaId: 'all',
  fanId: 'all',
  fanName: '',
  value: ['all', 'all', 'all']
};
class TreeFormat extends Component {
  constructor(props) {
    super(props);
    this.state = {
      value: ['all', 'all', 'all'],
      fanName: ''
    };
  }
  componentWillUnmount() {
    paramsSearch = {
      parentAreaId: 'all',
      sonAreaId: 'all',
      fanId: 'all',
      fanName: ''
    };
  }
  //树形结构加载
  renderTreeNodes = (data, id) => {
    return data.map((item, index) => {
      // console.log(index);
      if (item.children) {
        return (
          <TreeNode
            title={
              <span className={styles.flex}>
                <span
                  className={cx(
                    styles[`${item.status && item.status.name}Circle`],
                    styles.circle
                  )}
                />
                {item.name}
              </span>
            }
            key={item.id}
            dataRef={item}
            value={item.id}
          >
            {this.renderTreeNodes(item.children, item.id)}
          </TreeNode>
        );
      }
      return (
        <TreeNode
          title={
            <span className={styles.flex}>
              <span
                className={cx(
                  styles[`${item.status && item.status.name}Circle`],
                  styles.circle
                )}
              />
              {item.name}
            </span>
          }
          key={item.id}
          dataRef={item}
          value={item.id}
        />
      );
    });
  };
  renderTree = () => {
    const { treeFan } = this.props;
    if (treeFan.error) return <Network title={'网络错误！'} />;
    if (treeFan.loading)
      return (
        <div className={styles.flexCol}>
          <Spin />
        </div>
      );
    const data = treeFan.treeFan;
    return (
      <div>
        <div className={styles.wrap}>
          <span className={styles.line} />
          <span>设备总数：{data.total}</span>
        </div>
        <Tree
          onSelect={this.select}
          style={{
            borderTop: '1px dashed',
            borderColor: 'rgba(255,255,255,.5)'
          }}
          defaultSelectedKeys={[getStore('fanId')]}
          defaultExpandedKeys={[getStore('fanId')]}
        >
          {this.renderTreeNodes(data.fanList)}
        </Tree>
      </div>
    );
  };
  //级联变化
  onChange = value => {
    this.setState({ value });
    paramsSearch.parentAreaId = value[0];
    paramsSearch.sonAreaId = value[1];
    paramsSearch.fanId = value[2];
    paramsSearch.value = ['all', 'all', 'all'];
    this.refetchData();
  };
  //搜索
  onSearch = value => {
    paramsSearch.fanName = value;
    this.refetchData();
  };
  refetchData = () => {
    this.props.treeFan.refetch(paramsSearch);
  };
  //选中
  select = (value, e) => {
    // debugger;
    if (!e.node.props.dataRef.children && value.length) {
      let data = e.node.props.dataRef;
      setStore('number', `${data.fanNumber} / ${data.motorNumber}`);
      setStore('fanId', value[0]);
      setStore('name', data.name);
      setStore('iconType', data.iconType);
      eventObject.loading.dispatch(true);
      eventObject.changeFan.dispatch(value[0], data.iconType);
    }
  };
  render() {
    return (
      <div className={styles.listContainer}>
        <Filter
          data={this.props.data}
          type={'treeCascadeSelect'}
          {...this.state}
          onChange={this.onChange}
          onSearch={this.onSearch}
        />
        {this.renderTree()}
      </div>
    );
  }
}
export default compose(
  withApollo,
  graphql(TreeCascadeSelect, {
    options: props => {
      return {};
    }
  }),
  graphql(TreeFan, {
    options: props => {
      return {
        variables: paramsSearch,
        fetchPolicy: 'cache-and-network'
      };
    },
    name: 'treeFan'
  })
)(TreeFormat);
