import React, { Component } from 'react';
import styles from './index.less';
import classNames from 'classnames';
import { compose, graphql, withApollo } from 'react-apollo';
import { CurrentReportStatus } from 'Api/graphql/alertDetail.graphql';
import { Spin } from 'antd';
import moment from 'moment';
import Feedback from '../Feedback';

const cx = classNames.bind(styles);
// const data = {
//   current: 'complete',
//   status: '持续中',
//   isFeedback: false
// };
class StepFormat extends Component {
  constructor(props) {
    super(props);
    this.data = null;
  }

  componentDidMount() {
    this.formatData(this.props);
  }
  componentWillReceiveProps(nextprops) {
    this.formatData(nextprops);
  }
  formatData = props => {
    const { data } = props;
    if (data.error || data.loading) return null;
    this.data = data.currentReportStatus;
  };
  getFeedbackIcon = data => {
    let icon;
    if (data.name === 'checked') {
      icon = 'checkedIcon';
    } else if (data.name === 'continuing') {
      icon = 'grayFeedbackIcon';
    } else {
      if (data.isFeedback) {
        icon = 'doneIcon';
      } else {
        icon = 'errorIcon';
      }
    }
    return icon;
  };
  renderContent = () => {
    const { data } = this.props;
    if (data.error) return <div>网络错误！</div>;
    if (data.loading) return <Spin />;
    const { currentReportStatus } = data;
    return (
      <div className={styles.bg}>
        <div
          className={cx(
            styles.progress,
            styles[`${currentReportStatus.name}Bg`]
          )}
        />
        <div className={styles.wrap}>
          <div className={styles.status}>
            <span
              className={cx(
                styles.icon,
                styles[
                  currentReportStatus.name === 'continuing'
                    ? 'continuingIcon'
                    : 'doneIcon'
                ]
              )}
            />
            <span
              className={cx(
                styles.text,
                styles[currentReportStatus.name === 'continuing' ? 'red' : '']
              )}
            >
              设备预警
            </span>
          </div>
          <div className={styles.status}>
            <span
              className={cx(
                styles.icon,
                styles[this.getFeedbackIcon(currentReportStatus)]
              )}
            />
            <span
              className={cx(
                styles.text,
                styles[currentReportStatus.name === 'checked' ? 'yellow' : '']
              )}
            >
              核查反馈
            </span>
          </div>
          <div className={styles.status}>
            <span
              className={cx(
                styles.icon,
                styles[
                  currentReportStatus.name === 'finished'
                    ? 'grayCompleteIcon'
                    : 'finishedIcon'
                ]
              )}
            />
            <span
              className={cx(
                styles.text,
                styles[currentReportStatus.name === 'finished' ? 'blue' : '']
              )}
            >
              正常运行
            </span>
          </div>
        </div>
        <div
          className={cx(
            styles.bubble,
            styles[`${currentReportStatus.name}Status`]
          )}
        >
          <span>当前时刻：{moment().format('HH:mm')}</span>
          <span>当前状态：{currentReportStatus.displayName}</span>
        </div>
      </div>
    );
  };
  //fetchData
  fetchData = () => {
    this.props.data.refetch();
  };
  render() {
    return (
      <div>
        <div className={styles.container}>
          <span className={styles.title}>预警状态</span>
          {this.renderContent()}
        </div>
        <div className={styles.result}>
          <span className={styles.resultTitle}>现场反馈</span>
          {this.data && (
            <Feedback
              data={this.data}
              id={this.props.id}
              fetchData={this.fetchData}
            />
          )}
        </div>
      </div>
    );
  }
}
export default compose(
  withApollo,
  graphql(CurrentReportStatus, {
    options: props => {
      return {
        variables: {
          messageId: props.id
        },
        errorPolicy: 'all',
        fetchPolicy: 'cache-and-network'
      };
    }
  })
)(StepFormat);
