import React, { Component } from 'react';
import styles from './index.less';
import { Link, withRouter } from 'react-router-dom';

@withRouter
class ThreeLevelMenu extends Component {
  render() {
    return (
      <div className={styles.container}>
        <div
          className={styles.flex}
          onClick={() => this.props.history.push('/')}
        >
          <span className={styles.back} />返回
        </div>
        <div className={styles.menu}>
          <Link
            to={'/Detail'}
            className={
              styles[this.props.current === 'Detail' ? 'selectStyle' : '']
            }
          >
            实时监测
          </Link>
          <Link
            to={'/PointDetails'}
            className={
              styles[this.props.current === 'PointDetails' ? 'selectStyle' : '']
            }
          >
            测点详情
          </Link>
          <Link
            to={'/AlertRecord'}
            className={
              styles[this.props.current === 'AlertRecord' ? 'selectStyle' : '']
            }
          >
            预警记录
          </Link>
          <Link
            to={'/Evaluation'}
            className={
              styles[this.props.current === 'Evaluation' ? 'selectStyle' : '']
            }
          >
            设备评估
          </Link>
        </div>
      </div>
    );
  }
}
export default ThreeLevelMenu;
