import React, { Component } from 'react';
import { Radio } from 'antd';
import styles from './index.less';
import ThreeLevelMenu from 'Components/ThreeLevelMenu';
import HistoryRecord from './HistoryRecord';
import SummaryChart from './SummaryChart';

export default class AlertRecord extends Component {
  constructor(props) {
    super(props);
    this.state = {
      value: 1
    };
  }

  change = e => {
    this.setState({ value: e.target.value });
  };
  render() {
    return (
      <div style={{ height: '100%' }}>
        <ThreeLevelMenu current={'AlertRecord'} />
        <div className={styles.container}>
          <Radio.Group
            defaultValue={this.state.value}
            onChange={this.change}
            style={{ marginBottom: 24 }}
          >
            <Radio value={1}>历史记录</Radio>
            <Radio value={2}>统计图表</Radio>
          </Radio.Group>
          {this.state.value === 1 ? (
            <HistoryRecord value={this.state.value} />
          ) : (
            <SummaryChart value={this.state.value} />
          )}
        </div>
      </div>
    );
  }
}
