import React, { Component } from 'react';
import ReactEcharts from 'echarts-for-react';
// import DataSource from 'Resource/json/efficiency.json';
import { compose, graphql, withApollo } from 'react-apollo';
import { FanPerformance } from 'Api/graphql/evaluation.graphql';
// import Empty from '../../../../components/Empty';
import Network from 'Components/Network';
import { Spin } from 'antd';
import styles from '../index.less';

class ScatterChart extends Component {
  formatOption = result => {
    return {
      tooltip: {
        show: true,
        trigger: 'item'
      },
      color: '#00FFD6',
      grid: {
        left: 62,
        right: 58,
        bottom: 25,
        top: 48
      },
      xAxis: {
        name: '流量Q（m³/s）',
        type: 'value',
        axisLine: {
          show: true,
          lineStyle: {
            color: '#fff'
          }
        },
        nameTextStyle: {
          padding: [0, 0, 35, -50]
        },
        splitLine: {
          show: false
        }
      },
      yAxis: {
        name: '总压升Pt（pa）',
        axisLine: {
          show: true,
          lineStyle: {
            color: '#fff'
          }
        },
        nameTextStyle: {
          padding: [0, 0, 0, 50]
        },
        splitLine: {
          show: false
        }
      },
      series: Object.keys(result).length
        ? this.getLabels(result.labels)
            .concat(this.getLines(result.lines))
            .concat(this.getData(result.data))
        : []
    };
  };
  getLabels = data => {
    let arr = [];
    data.map(value => {
      let obj = {
        data: [],
        type: 'line',
        smooth: true,
        symbol: 'circle',
        symbolSize: 1,
        label: {
          show: true,
          formatter: value.label,
          color: '#fff',
          position: 'right'
        }
      };
      obj.data.push([value.x, value.y]);
      return arr.push(obj);
    });
    return arr;
  };
  getLines = data => {
    if (!data.length) return;
    let arr = [];
    data.map(value => {
      let obj = {
        data: [],
        type: 'line',
        smooth: true,
        symbol: 'none',
        lineStyle: {
          color: '#1CD1FF',
          width: 1
        }
      };
      obj.data = value;
      return arr.push(obj);
    });
    return arr;
  };
  getData = data => {
    let arr = [];
    let obj = {
      data: [],
      type: 'line',
      smooth: true,
      symbol: 'circle',
      symbolSize: 14,
      tooltip: {
        show: true,
        trigger: 'item',
        formatter: `Q:${data.x} Pt:${data.y}  n:${data.n} η:${data.eta}`
      }
    };
    obj.data.push([data.x, data.y]);
    arr.push(obj);
    return arr;
  };
  render() {
    const { data } = this.props;
    if (data.error) return <Network />;
    if (data.loading) return <Spin />;
    // console.log(data);
    const result = data.fanPerformance;
    let onEvents = {
      click: this.props.onClick
    };
    let option = this.formatOption(result);
    return (
      <div className={styles.flexColumn}>
        <ReactEcharts
          option={option}
          style={{ width: '100%', height: '80%' }}
          theme="my_theme"
          onEvents={onEvents}
        />
        <span style={{ marginTop: 20 }}>已选时刻：{this.props.time}</span>
      </div>
    );
  }
}
export default compose(
  withApollo,
  graphql(FanPerformance, {
    options: props => {
      return {
        variables: {
          fanId: props.fanId,
          dateTime: props.time
        },
        errorPolicy: 'all',
        fetchPolicy: 'cache-and-network'
      };
    }
  })
)(ScatterChart);
