import React from 'react';
import styles from './index.less';
import { Menu, Dropdown, Icon, Button } from 'antd';
import classNames from 'classnames';
import PropTypes from 'prop-types';

let cx = classNames.bind(styles);
// @translate(['common'])
export default class DropMenu extends React.Component {
  static propTypes = {
    data: PropTypes.array,
    title: PropTypes.string,
    className: PropTypes.string,
    initValue: PropTypes.oneOfType([PropTypes.string, PropTypes.number]),
    disabled: PropTypes.bool,
    callBack: PropTypes.func
  };
  constructor(props) {
    super(props);
    this.state = {
      value: this.initValue(this.props).displayName,
      key: this.initValue(this.props).id
    };
  }
  componentWillReceiveProps(nextProps, nextState) {
    if (this.initValue(nextProps)) {
      this.setState({
        value: this.initValue(nextProps).displayName,
        key: this.initValue(nextProps).id
      });
    }
  }
  initValue(props) {
    let { initValue, data } = props,
      item = {
        displayName: '全部',
        id: 'all'
      };
    if (!initValue || initValue === 'all') return item;
    if (initValue) {
      if (typeof initValue === 'string') {
        item = data.find(value => {
          return value.id === initValue;
        });
      } else {
        item = data.find(value => {
          return parseInt(value.id) === parseInt(initValue);
        });
      }
    }
    return item;
  }
  //点击下拉菜单
  handleMenuClick = e => {
    this.setState({
      value: e.item.props.children,
      key: e.key
    });
    this.props.callBack && this.props.callBack(e);
  };
  renderMenuItem = data => {
    return (
      <Menu
        style={this.props.dropStyle}
        onClick={e => this.handleMenuClick(e)}
      >
        {!this.props.isAll && <Menu.Item key={'all'}>全部</Menu.Item>}
        {data.map((item, i) => {
          return <Menu.Item key={item.id}>{item.displayName}</Menu.Item>;
        })}
      </Menu>
    );
  };
  render() {
    const { data, className, title, disabled, otherClassName } = this.props;
    let menu = this.renderMenuItem(data || []);
    return (
      <div
        className={cx(styles[otherClassName || 'wrap'], className)}
        style={this.props.style}
      >
        <span className={styles.text}>{title}</span>
        <Dropdown overlay={menu} placement={'bottomLeft'} disabled={disabled}>
          <Button className={styles.button}>
            {this.state.value}
            <Icon type="down" />
          </Button>
        </Dropdown>
      </div>
    );
  }
}
