import React, { Component } from 'react';
import * as THREE from 'three';
import styles from './index.less';
import TrackballControls from 'three-trackballcontrols';
import eventObject from 'Configs/eventSignal';
import { GLTFLoader } from 'three/examples/jsm/loaders/GLTFLoader';
export default class Test extends Component {
  constructor(props) {
    super(props);
    this.state = {
      loading: true
    };
    this.diagnosis_fan_ubf = null;
    this.renderer = null;
    this.scene = new THREE.Scene(); //场景构建
    this.camera = null;
    this.trackballControls = null;
    this.color = '#fff';
    this.obj = this.props.obj;
    this.objects = {};
  }
  componentDidMount() {
    this.initData();
    window.addEventListener('resize', () => this.resize());
    eventObject.mouse.add(this.handleHover);
    eventObject.scale.add(this.resize);
    eventObject.changeModal.add(this.changeModal);
  }
  componentWillUnmount() {
    window.removeEventListener('resize', () => this.resize());
    eventObject.mouse.remove(this.handleHover);
    eventObject.scale.remove(this.resize);
    eventObject.changeModal.remove(this.changeModal);
    this.clearScene();
  }
  componentWillReceiveProps(nextProps) {
    nextProps.obj.map(value => {
      let child = this.objects[value.name];
      if(child){
        this.setChildColor(child, this.getColor(value.status));
      }
    });
  }
  changeModal = obj => {
    this.clearScene();
    this.init(obj);
    this.reset();
    this.renderScene();
  };
  clearScene = () => {
    this.animate && cancelAnimationFrame(this.animate);
    this.deleteGroup(this.group);
  };
  //卸载模型
  deleteGroup = group => {
    if (!group) return;
    // 删除掉所有的模型组内的mesh
    group.children.map(item => {
      if (item instanceof THREE.Mesh) {
        item.geometry.dispose(); // 删除几何体
        item.material.dispose(); // 删除材质
        this[item.name] = null;
      }
    });
    this.scene.remove(this.group);
  };
  //尺寸变化
  resize = () => {
    if (this.fan) {
      this.renderer.setSize(this.fan.offsetWidth, this.fan.offsetHeight);
    }
  };
  //选中效果
  handleHover = props => {
    if (props.current && this.objects[props.current]) {
      let child = this.objects[props.current];
      if (props.move) {
        this.setChildColor(child, '0x00FFD6');
      } else {
        let obj = this.obj.filter(item => item.name === props.current);
        if (obj.length) {
          this.setChildColor(child, this.getColor(obj[0].status));
        }
      }
      this.resize();
    }
  };
  setChildColor = (child, color) => {
    if (child.isMesh) {
      if (child.material && child.material[0]) {
        child.material[0].color.setHex(color);
      }
      if (child.material && child.material.color) {
        child.material.color.setHex(color);
      }
    }
  };
  //初始化数据
  initData = () => {
    this.initCamera();
    this.initRender();
    this.init(this.obj);
    this.renderTrackballControls();
    this.renderSpotLight();
    this.renderScene();
  };
  //相机构建
  initCamera = () => {
    this.camera = new THREE.PerspectiveCamera(
      35,
      window.innerWidth / window.innerHeight,
      1,
      15
    );
    //第一个参数改角度   第三个参数更改默认进来的显示比例
    this.camera.position.set(-1.3, 1, 3);
    this.camera.lookAt(0, 0, 0);
  };
  //渲染器构建1
  initRender = () => {
    this.renderer = new THREE.WebGLRenderer({ antialias: true, alpha: true }); //渲染器构建
    this.renderer.setClearAlpha(0);
    this.renderer.setSize(this.fan.offsetWidth, this.fan.offsetHeight); //window.innerWidth, window.innerHeight
    //this.renderer.shadowMap.enabled = true; //激活阴影
  };
  //模型加载
  init = obj => {
    //构建一个坐标轴
    /* const axes = new THREE.AxisHelper(10);
     this.scene.add(axes);*/
    const loader = new GLTFLoader();
     var a = new Date().getTime() / 1000
    //loader.setDRACOLoader(new DRACOLoader());
    //风机箱
    obj.map(value => {
      loader.load(
         require('../../../../'+value.url),
        gltf => {
          gltf.scene.scale.set(0.0003, 0.0003, 0.0003);
          gltf.scene.position.x = value.position.x;
          gltf.scene.position.y = value.position.y;
          gltf.scene.position.z = value.position.z;
          gltf.scene.traverse(child => this.processGLTFChild(child, value));
          this.scene.add(gltf.scene);
           console.log((new Date().getTime() / 1000) - a + " s")
        },
        xhr => {
          //console.log(`${(xhr.loaded / xhr.total) * 100}% loaded`);
        },
        error => {
          // called when loading has errors
          console.error('An error happened', error);
        }
      );
    });
    
    this.fan.appendChild(this.renderer.domElement);
  };
  processGLTFChild = (child, value) => {
    let color = this.getColor(value.status);
    
    try {
      if (child.isMesh) {
          child.geometry.center(); // center here
         /* child.castShadow = true
          child.receiveShadow = true*/
        this.objects[value.name] = child;
        // 有的child.material 类型是 Array, 有的是 Object
        if (child.material && child.material[0]) {
          child.material[0].color.setHex(color);
        }
        if (child.material && child.material.color) {
          child.material.color.setHex(color);
        }
      }
    } catch (e) {
      console.log('error:', e);
      console.error('设置色彩出错, child:', child);
    }
  };
  renderTrackballControls = () => {
    this.trackballControls = new TrackballControls(
      this.camera,
      this.renderer.domElement
    );
    this.trackballControls.rotateSpeed = 3.0;
    this.trackballControls.zoomSpeed = 2.0;
    this.trackballControls.panSpeed = 1.0;
    this.trackballControls.noPan = true;
    this.trackballControls.minDistance = 1.9;
    this.trackballControls.maxDistance = 14.5;
    this.trackballControls.dynamicDampingFactor = 0.3;
  };
  //添加材质灯光阴影
  renderSpotLight = () => {
    let light = new THREE.HemisphereLight('#ccdcdf', '#2846e4', 1);
    light.position.set(100, 150, 100);
    this.scene.add(light);
  };

  renderScene = () => {
    this.animate = requestAnimationFrame(this.renderScene);
    var clock = new THREE.Clock();
    var delta = clock.getDelta();

    this.trackballControls.update(delta);
    eventObject.loading.dispatch(false);
    if (
      this.objects.diagnosis_fan_ubf &&
      this.objects.diagnosis_bearing_FN &&
      this.objects.diagnosis_cpl_misaf &&
      this.objects.box
    ) {
     
      if (this.props.rotate !== 'stop') {
        this.objects.diagnosis_fan_ubf.rotation.x -= 0.02;
        this.objects.diagnosis_cpl_misaf.rotation.x -= 0.02;
        this.renderer.render(this.scene, this.camera);
      }
      this.renderer.render(this.scene, this.camera);
    }
  };
  ////创建控件并绑定在相机上
  getColor = type => {
    let color;
    switch (type) {
      case 'fine':
        color = '0xffffff';
        break;
      case 'fault':
        color = '0xDE7070';
        break;
      case 'warning':
        color = '0xFFE676';
        break;
      default:
        color = '0xffffff';
        break;
    }
    return color;
  };
  reset = () => {
    this.initCamera();
    this.renderTrackballControls();
  };
  render() {
    return (
      <div className={styles.wrap}>
        <div className={styles.status}>
          状态：{this.props.rotate === 'stop' ? '停机' : '运行'}
        </div>
        <button onClick={this.reset} className={styles.reset}>
          <span className={styles.icon} />
          一键复位
        </button>
        <div
          ref={ref => (this.fan = ref)}
          style={{ width: '100%', height: '100%' }}
        />
      </div>
    );
  }
}
