import echarts from 'echarts';
// register theme object
echarts.registerTheme('my_theme', {
  legend: {
    textStyle: {
      color: '#fff'
    }
  },
  color: ['#00FFD6', '#1CD1FF', '#FFAE5C', '#F1677D', '#E96659'],
  title: {
    textStyle: {
      color: '#fff',
      fontSize: 20,
      align: 'center',
      textShadowColor: 'rgba(38,234,253,.5)',
      textShadowBlur: 4,
      textShadowOffsetY: 2,
      lineHeight: 28,
      fontWeight: 'bold',
    },
    x: 'center',
    y: 'center'
  },
  noDataLoadingOption: {
    text: '暂无数据!'
  },
  tooltip: {
    trigger: 'axis',
    backgroundColor: '#fff',
    textStyle: {
      color: '#003378'
    },
    axisPointer: {
      type: 'cross',
      label: {
        backgroundColor: '#fff',
        color: '#003378'
      }
    }
  },
  //类目录
  categoryAxis: {
    axisLine: {
      show: false
    },
    axisTick: {
      show: false
    },
    axisLabel: {
      color: '#fff',
      fontSize: 14
    },
    nameTextStyle: {
      color: '#fff',
      fontSize: 14,

      padding: [25, 0, 0, 0]
    }
  },
  //时间目录
  timeAxis: {
    axisLine: {
      show: false
    },
    axisTick: {
      show: false
    },
    axisLabel: {
      color: '#4A4A4A',
      fontSize: 14,

      formatter: function(value, index) {
        var date = new Date(value);
        var texts = [date.getHours(), '00'];
        return texts.join(':');
      }
    },
    splitLine: {
      show: false
    },
    nameTextStyle: {
      color: '#4A4A4A',
      fontSize: 14,

      padding: [25, 0, 0, 0]
    }
  },
  // 数值型坐标轴默认参数
  valueAxis: {
    //坐标轴线
    axisLine: {
      show: false
    },
    //坐标轴刻度
    axisTick: {
      show: false
    },
    //坐标轴刻度标签
    axisLabel: {
      color: '#fff',
      fontSize: 14
    },
    // minInterval: 1,
    //坐标轴名称样式
    nameTextStyle: {
      color: '#fff',
      fontSize: 14,

      padding: [0, 0, 0, -35]
    },
    //网格线
    splitLine: {
      lineStyle: {
        color: '#fff',
        width: 0.3,
        type: 'dotted'
      }
    }
  }
});
