import React, { Component } from 'react';
// import echarts from 'echarts';
import ReactEcharts from 'echarts-for-react';
// import DataSource from 'Resource/json/health.json';
import { Spin } from 'antd';
import styles from '../index.less';
import Network from '../../../../components/Network';
import { compose, graphql, withApollo } from 'react-apollo';
import { HealthIndexTimeSeries } from 'Api/graphql/evaluation.graphql';

class Option extends Component {
  constructor(props) {
    super(props);
    this.state = {
      time: ''
    };
  }
  formatOption = data => {
    return {
      // color: '#FFAE5C',
      grid: {
        left: 60,
        right: 20,
        top: 70
      },
      xAxis: {
        type: 'category',
        data: data.timeSeries.keys || [],
        symbolOffset: ['50%', 0]
      },
      yAxis: {
        type: 'value',
        name: '分',
        minInterval: 1,
        max: 100
      },
      tooltip: {
        show: true,
        trigger: 'axis'
      },
      dataZoom: [
        {
          type: 'slider',
          show: true,
          textStyle: {
            color: '#fff'
          },
          dataBackground: {
            lineStyle: {
              color: '#fff'
            },
            areaStyle: { color: 'rgba(255,255,255,1)' }
          }
        },
        {
          type: 'inside'
        }
      ],
      visualMap: [
        {
          show: false,
          seriesIndex: 0,
          pieces: [
            {
              gte: 80.0,
              color: '#00ffd6'
            },
            {
              gte: 60.0,
              lte: 79.9,
              color: '#ffae5c'
            },
            {
              lt: 59.9,
              color: '#f1677d'
            }
          ],
          outOfRange: {
            color: '#00ffd6'
          }
        }
      ],
      series: [
        {
          data: data.timeSeries.data,
          type: 'line',
          // smooth: true,
          // symbol: 'none',
          name: '',
          markPoint: {
            data: this.getPoint(data.changePoints),
            label: {
              show: false
            },
            symbol: 'circle',
            symbolSize: 12
          }
        }
      ]
    };
  };
  getPoint = changePoints => {
    let arr = [];
    changePoints.map(value => {
      let obj = {
        name: {},
        coord: [],
        itemStyle: { color: '', borderWidth: 2, borderColor: '#fff' }
      };
      obj.name = value.changePoint;
      obj.coord = value.coord;
      if (value.color === 'red') {
        obj.itemStyle.color = '#E96659';
      } else {
        obj.itemStyle.color = '#57D3C6';
      }
      return arr.push(obj);
    });
    return arr;
  };
  render() {
    let onEvents = {
      click: this.props.onClick,
      dblclick: this.props.dblclick
    };
    const { data } = this.props;
    if (data.error) return <Network title={'网络错误！'} />;
    if (data.loading)
      return (
        <div className={styles.flexCol} style={{ width: '60%' }}>
          <Spin />
        </div>
      );
    const { healthIndexTimeSeries } = data;
    let option = this.formatOption(healthIndexTimeSeries);
    return (
      <ReactEcharts
        option={option}
        style={{ width: '60%', height: '90%' }}
        theme="my_theme"
        onEvents={onEvents}
      />
    );
  }
}
export default compose(
  withApollo,
  graphql(HealthIndexTimeSeries, {
    options: props => {
      return {
        variables: {
          fanId: props.fanId
        },
        errorPolicy: 'all',
        fetchPolicy: 'cache-and-network'
      };
    }
  })
)(Option);
