import React, { Component } from 'react';
import styles from './index.less';
import { Cascader, Spin } from 'antd';
import Tree from './tree';
// import DeviceData from 'Resource/json/device.json';
import { compose, graphql, withApollo } from 'react-apollo';
import {
  AreaFanSelect,
  StatisticAndCheckpointTree
} from 'Api/graphql/pointDetails.graphql';
import Content from '../Content';
import { getStore, setStore } from '../../../api/util';

class SideEdge extends Component {
  constructor(props) {
    super(props);
    this.state = {
      show: true,
      value: [
        getStore('parentAreaId'),
        getStore('sonAreaId'),
        getStore('fanId')
      ],
      fanId: getStore('fanId'),
      leftParamId: '',
      treeValue: []
    };
  }
  componentDidMount() {
    this.formatData(this.props);
  }
  componentWillReceiveProps(nextProps) {
    this.formatData(nextProps);
  }
  formatData = props => {
    const { statisticAndCheckpointTree } = props;
    if (statisticAndCheckpointTree.error || statisticAndCheckpointTree.loading)
      return null;
    const {
      checkpointTree
    } = statisticAndCheckpointTree.statisticAndCheckpointTree;
    if (checkpointTree.length) {
      if (checkpointTree[0].children.length) {
        let leftParamId = checkpointTree[0].children[0].value;
        this.setState({
          treeValue: [leftParamId],
          leftParamId: leftParamId
        });
      }
    }
  };

  //隐藏与显示
  toggle = () => {
    this.setState({ show: !this.state.show });
  };
  //级联选择切换
  change = (value, selectedOptions) => {
    this.setState({ value: value, fanId: value[2] });
    setStore('fanId', value[2]);
    setStore(
      'number',
      `${selectedOptions[2].fanNumber} / ${selectedOptions[2].motorNumber}`
    );
    setStore('name', selectedOptions[2].label);
    setStore('iconType', selectedOptions[2].iconType);
    setStore('parentAreaId', value[0]);
    setStore('sonAreaId', value[1]);
    this.props.statisticAndCheckpointTree.refetch({ fanId: value[2] });
  };
  onTreeChange = selectKeys => {
    this.setState({ leftParamId: selectKeys[0] });
  };
  renderCascader = () => {
    const { data } = this.props;
    if (data.error) return <div>网络错误!</div>;
    if (data.loading) return <Spin />;
    const { areaFanSelect } = data;
    return (
      <Cascader
        options={areaFanSelect}
        value={this.state.value}
        placeholder="Please select"
        allowClear={false}
        style={{ width: '72%', marginLeft: '8px' }}
        onChange={this.change}
      />
    );
  };
  render() {
    return (
      <div className={styles.container}>
        {this.state.show ? (
          <div className={styles.containerLeft}>
            <div className={styles.wrap}>
              <span className={styles.title}>数据点位选择</span>
              <span className={styles.close} onClick={this.toggle} />
            </div>
            <div className={styles.flex}>
              <span>设备定位</span>
              {this.renderCascader()}
            </div>
            <Tree
              onChange={this.onTreeChange}
              {...this.props}
              {...this.state}
            />
          </div>
        ) : (
          <span className={styles.open} onClick={this.toggle} />
        )}
        <div
          className={styles.middle}
          style={{
            width: !this.state.show ? '100%' : 'calc(100% - 308px)'
          }}
        >
          <Content {...this.state} />
        </div>
      </div>
    );
  }
}
export default compose(
  withApollo,
  graphql(AreaFanSelect, {
    options: props => {
      return {
        errorPolicy: 'all',
        fetchPolicy: 'cache-and-network'
      };
    }
  }),
  graphql(StatisticAndCheckpointTree, {
    options: props => {
      return {
        variables: { fanId: getStore('fanId') },
        errorPolicy: 'all',
        fetchPolicy: 'cache-and-network'
      };
    },
    name: 'statisticAndCheckpointTree'
  })
)(SideEdge);
