import React, { Component } from 'react';
import ReactEcharts from 'echarts-for-react';
// import DataSource from 'Resource/json/efficiency.json';
import { compose, graphql, withApollo } from 'react-apollo';
import { EfficiencyTimeSeries } from 'Api/graphql/evaluation.graphql';
import Network from '../../../../components/Network';
import { Spin } from 'antd';
import styles from '../index.less';

class Option extends Component {
  formatOption = data => {
    return {
      // color: '#00FFD6',
      grid: {
        left: 60,
        right: 20
      },
      xAxis: {
        type: 'category',
        data: data.keys || [],
        symbolOffset: ['50%', 0]
      },
      yAxis: {
        type: 'value',
        name: '%',
        // max: 100,
        // min: 70,
        minInterval: 1
      },
      tooltip: {
        show: true,
        trigger: 'axis'
      },
      dataZoom: [
        {
          type: 'slider',
          show: true,
          textStyle: {
            color: '#fff'
          },
          dataBackground: {
            lineStyle: {
              color: '#fff'
            },
            areaStyle: { color: 'rgba(255,255,255,1)' }
          }
        },
        {
          type: 'inside'
        }
      ],
      visualMap: [
        {
          show: false,
          seriesIndex: 0,
          pieces: [
            {
              gte: 80.0,
              color: '#00ffd6'
            },
            {
              gte: 60.0,
              lte: 79.9,
              color: '#ffae5c'
            },
            {
              lt: 59.9,
              color: '#f1677d'
            }
          ],
          outOfRange: {
            color: '#00ffd6'
          }
        }
      ],
      series: [
        {
          data: data.data,
          type: 'line',
          // smooth: true,
          name: ''
        }
      ]
    };
  };
  render() {
    let onEvents = {
      click: this.props.onClick
    };
    const { data } = this.props;
    if (data.error) return <Network />;
    if (data.loading)
      return (
        <div className={styles.flexCol} style={{ width: '60%' }}>
          <Spin />
        </div>
      );
    const { efficiencyTimeSeries } = data;
    let option = this.formatOption(efficiencyTimeSeries);
    return (
      <ReactEcharts
        option={option}
        style={{ width: '60%', height: '90%' }}
        theme="my_theme"
        onEvents={onEvents}
      />
    );
  }
}
export default compose(
  withApollo,
  graphql(EfficiencyTimeSeries, {
    options: props => {
      return {
        variables: {
          fanId: props.fanId
        },
        errorPolicy: 'all',
        fetchPolicy: 'cache-and-network'
      };
    }
  })
)(Option);
