import React, { Component } from 'react';
import styles from './index.less';
import FormFormat from './form';
import DiagnosticDetails from './DiagnosticDetails';
import { withRouter } from 'react-router-dom';

@withRouter
class AlertDetail extends Component {
  constructor(props) {
    super(props);
    this.id = '';
    this.iconType = '';
  }

  goBack = () => {
    this.props.history.push('/AlertRecord');
  };
  render() {
    if (this.props.history.location.state) {
      this.id = this.props.history.location.state.id;
      this.iconType = this.props.history.location.state.iconType;
    }
    return (
      <div className={styles.container}>
        <div className={styles.deviceTitle} onClick={this.goBack}>
          <span className={styles.back} />返回
        </div>
        {this.id && <DiagnosticDetails {...this} />}
        {this.id && <FormFormat {...this} />}
      </div>
    );
  }
}
export default AlertDetail;
