import React, { Component } from 'react';
import styles from './index.less';
import { Collapse, Icon, Spin } from 'antd';
import classNames from 'classnames';
import { compose, graphql, withApollo } from 'react-apollo';
import {
  MatrixCascadeSelect,
  MatrixFan
} from 'Api/graphql/deviceDetail.graphql';
import Network from 'Components/Network';
import Empty from 'Components/Empty';
import Filter from './filter';
import Url from 'Api';
import eventObject from 'Configs/eventSignal';
import { getStore, setStore } from '../../api/util';

const cx = classNames.bind(styles);
const Panel = Collapse.Panel;
let paramsSearch = {
  value: ['all', 'all'],
  fanId: 'all',
  fanTypeId: 'all',
  fanName: ''
};
class List extends Component {
  constructor(props) {
    super(props);
    this.state = {
      value: ['all', 'all'],
      fanName: '',
      checked: getStore('fanId')
    };
  }
  componentWillUnmount() {
    paramsSearch = {
      fanId: 'all',
      fanTypeId: 'all',
      fanName: ''
    };
  }
  //渲染列表
  renderContent = () => {
    const { matrixFan } = this.props;
    if (matrixFan.error) return <Network title={'网络错误！'} />;
    if (matrixFan.loading)
      return (
        <div className={styles.flexCol}>
          <Spin />
        </div>
      );
    const data = matrixFan.matrixFan;
    return (
      <div>
        <div className={styles.wrap}>
          <span className={styles.line} />
          <span>设备总数：{data.total}</span>
        </div>
        <Collapse
          bordered={false}
          defaultActiveKey={['0']}
          expandIcon={({ isActive }) => (
            <Icon type="caret-right" rotate={isActive ? 90 : 0} />
          )}
        >
          {data.fanList.length ? (
            data.fanList.map((value, index) => {
              return (
                <Panel
                  header={value.typeName}
                  key={index}
                  className={styles.panel}
                >
                  {value.children.map((item, key) => {
                    return (
                      <div
                        className={styles.list}
                        key={key}
                        onClick={() => this.changeFan(item)}
                      >
                        <div
                          className={cx(
                            styles.listWrap,
                            styles[
                              this.state.checked === item.id ? 'selected' : ''
                            ]
                          )}
                        >
                          <div
                            className={styles.fan}
                            style={{
                              background: `url('${Url}${
                                item.imgUrl
                              }') no-repeat center/cover`
                            }}
                          />
                          <span
                            className={cx(
                              styles.triangle,
                              styles[item.status && item.status.name]
                            )}
                          />
                        </div>
                        <span>设备名称：{item.name}</span>
                        <span>
                          所属区域：{item.area && item.area.displayName}
                        </span>
                      </div>
                    );
                  })}
                </Panel>
              );
            })
          ) : (
            <Empty />
          )}
        </Collapse>
      </div>
    );
  };
  //级联变化
  onChange = value => {
    this.setState({ value });
    paramsSearch.fanTypeId = value[0];
    paramsSearch.fanId = value[1];
    paramsSearch.value = ['all', 'all'];
    this.refetchData();
  };
  //搜索
  onSearch = value => {
    paramsSearch.fanName = value;
    this.refetchData();
  };
  refetchData = () => {
    this.props.matrixFan.refetch(paramsSearch);
  };
  //风机切换
  changeFan = item => {
    setStore('fanId', item.id);
    setStore('number', `${item.fanNumber} / ${item.motorNumber}`);
    setStore('name', item.name);
    setStore('iconType', item.iconType);
    setStore('parentAreaId', item.area.parent.id);
    setStore('sonAreaId', item.area.id);
    this.setState({ checked: item.id });
    eventObject.loading.dispatch(true);
    eventObject.changeFan.dispatch(item.id, item.iconType);
  };
  render() {
    return (
      <div className={styles.listContainer}>
        <Filter
          data={this.props.data}
          type={'matrixCascadeSelect'}
          {...this.state}
          onChange={this.onChange}
          onSearch={this.onSearch}
        />
        {this.renderContent()}
      </div>
    );
  }
}
export default compose(
  withApollo,
  graphql(MatrixCascadeSelect, {
    options: props => {
      return {};
    }
  }),
  graphql(MatrixFan, {
    options: props => {
      return {
        variables: paramsSearch,
        fetchPolicy: 'cache-and-network'
      };
    },
    name: 'matrixFan'
  })
)(List);
