import React, { Component } from 'react';
import Network from 'Components/Network';
import { Spin } from 'antd';
import { CheckpointCompareChartData } from 'Api/graphql/pointDetails.graphql';
import { compose, graphql, withApollo } from 'react-apollo';
import Option from './option';
import styles from '../index.less';

class Chart extends Component {
  render() {
    const { data } = this.props;
    if (data.error) return <Network />;
    if (data.loading) return <Spin />;
    let chartData = data.checkpointCompareChartData;
    let style = {};
    if (chartData.length === 1) {
      style = { width: '100%', height: '720px' };
    } else {
      style = { width: '49.5%', height: '350px' };
    }
    return (
      <div className={styles.chartWrap}>
        {chartData.map((value, index) => {
          return <Option data={value} key={index} style={style} />;
        })}
      </div>
    );
  }
}
export default compose(
  withApollo,
  graphql(CheckpointCompareChartData, {
    options: props => {
      return {
        variables: {
          fanId: props.fanId,
          startTime: props.startTime,
          endTime: props.endTime,
          leftParamId: props.leftParamId,
          rightParamId: props.rightParamId,
          dataFreq: props.dataFreq
        },
        errorPolicy: 'all',
        fetchPolicy: 'cache-and-network'
      };
    }
  })
)(Chart);
