import React, { Component } from 'react';
// import echarts from 'echarts';
// import DataSource from 'Resource/json/health.json';
import styles from '../index.less';
import RadarChart from './RadarChart';
import Chart from './Option';
import { Modal, Icon } from 'antd';
import moment from 'moment';

export default class LineChart extends Component {
  constructor(props) {
    super(props);
    this.state = {
      time: moment().format('YY/MM/DD HH') + ':00'
    };
  }
  //点击事件
  onClick = e => {
    if (e.componentType === 'markPoint') {
      let { coord } = e.data;
      this.setState({ time: coord[0] });
    } else if (e.componentType === 'series') {
      this.setState({ time: e.name });
    }
  };
  //双击事件
  onDblClick = e => {
    if (e.componentType === 'markPoint') {
      // console.log(e);
      Modal.info({
        title: (
          <div
            style={{ display: 'flex', alignItems: 'center', marginLeft: 36 }}
          >
            <Icon
              type="info-circle"
              style={{ color: e.color, fontSize: 20, marginRight: 4 }}
            />
            {e.name.title}
          </div>
        ),
        content: (
          <div style={{ marginTop: 20 }}>
            {e.name.content.map((value, index) => {
              return (
                <div key={index} className={styles.listWrap}>
                  <p>{value.currentTime}</p>
                  <p>预警编号：{value.number}</p>
                  <p>预警原因：{value.reason}</p>
                  <div className={styles.listLine} />
                </div>
              );
            })}
          </div>
        )
      });
    }
  };
  render() {
    // console.log(this.state.time);
    return (
      <div className={styles.chartWrap}>
        <Chart
          onClick={this.onClick}
          dblclick={this.onDblClick}
          {...this.props}
        />
        <div className={styles.panel} style={{ width: '40%' }}>
          <RadarChart {...this.state} {...this.props} />
        </div>
      </div>
    );
  }
}
