import React, { Component } from 'react';
import styles from './index.less';
import { Button, Checkbox, Col, Input, Radio, Row, message } from 'antd';
import { compose, graphql, withApollo } from 'react-apollo';
import {
  FeedbackEditInfo,
  CreateUpdateFeedback
} from 'Api/graphql/alertDetail.graphql';
import eventObject from 'Configs/eventSignal';

const RadioGroup = Radio.Group,
  { TextArea } = Input;

class FeedbackEdit extends Component {
  constructor(props) {
    super(props);
    this.state = {
      value: 1,
      conclusionId: [],
      comment: '',
      isAgree: true,
      feedbackId: ''
    };
  }
  componentDidMount() {
    if (this.props.query) {
      this.fetchData();
    }
  }
  componentWillReceiveProps(nextprops) {
    if (nextprops.query) {
      this.fetchData();
    }
  }
  fetchData = () => {
    this.props.client
      .query({
        query: FeedbackEditInfo,
        variables: { messageId: this.props.id },
        errorPolicy: 'all',
        fetchPolicy: 'network-only'
      })
      .then(data => {
        const { feedbackEditInfo } = data.data;
        this.setState({
          feedbackId: feedbackEditInfo.id,
          value: feedbackEditInfo.isAgree ? 1 : 2,
          conclusionId: feedbackEditInfo.conclusion,
          comment: feedbackEditInfo.comment
        });
      });
  };
  //多选框
  checkChange = e => {
    this.setState({ conclusionId: e });
  };
  //文本框改变
  textChange = e => {
    this.setState({ comment: e.target.value });
  };
  //单选框改变
  change = e => {
    this.setState({ value: e.target.value });
    if (e.target.value === 1) {
      this.setState({ isAgree: true });
    } else {
      this.setState({ isAgree: false });
    }
  };
  confirm = () => {
    let inputData = {
      isAgree: this.state.isAgree,
      conclusionId: this.state.conclusionId,
      comment: this.state.comment,
      messageId: this.props.id
    };
    this.props
      .createUpdateFeedback({
        variables: { inputData, feedbackId: this.state.feedbackId }
      })
      .then(res => {
        if (res.data.createUpdateFeedback.ok) {
          message.success('提交成功！');
          this.props.seeResult();
          this.props.fetchData();
          eventObject.status.dispatch();
        }
      });
  };
  //cancel
  cancel = () => {
    this.props.goBack(this.props.view);
  };
  //重置
  reset = () => {
    this.setState({
      value: 1,
      conclusionId: [],
      comment: '',
      isAgree: true,
      feedbackId: ''
    });
  };
  render() {
    return (
      <div>
        <div className={styles.bg}>
          <div style={{ marginBottom: 16 }}>
            <div className={styles.title}>核查结论</div>
            <span style={{ width: 150 }}>与预警信息一致：</span>
            <RadioGroup value={this.state.value} onChange={this.change}>
              <Radio value={1}>是</Radio>
              <Radio value={2}>否</Radio>
            </RadioGroup>
          </div>
          <div>
            <div className={styles.title}>对策措施</div>
            <Checkbox.Group
              style={{ width: '100%' }}
              onChange={this.checkChange}
              value={this.state.conclusionId}
            >
              <Row>
                <Col span={12} style={{ marginBottom: 10 }}>
                  <Checkbox value="A">立即停机检修/换新</Checkbox>
                </Col>
                <Col span={12} style={{ marginBottom: 10 }}>
                  <Checkbox value="B">本次关机后检修/换新</Checkbox>
                </Col>
                <Col span={12} style={{ marginBottom: 10 }}>
                  <Checkbox value="C">定期维保统一检修/换新</Checkbox>
                </Col>
                <Col span={12} style={{ marginBottom: 10 }}>
                  <Checkbox value="D">不予处理</Checkbox>
                </Col>
              </Row>
            </Checkbox.Group>
          </div>
          <div className={styles.result}>
            <div className={styles.title}>维修结论</div>
            <TextArea
              placeholder="请输入文字(不超过200字)"
              style={{ height: 119, resize: 'none' }}
              maxLength={200}
              value={this.state.comment}
              onChange={this.textChange}
            />
          </div>
        </div>
        <div className={styles.btn}>
          <Button
            ghost
            style={{ marginRight: 8, width: 120 }}
            onClick={this.confirm}
            type={'primary'}
          >
            提交
          </Button>
          {this.props.name === 'continuing' ? (
            <Button ghost onClick={this.reset} style={{ width: 120 }}>
              重置
            </Button>
          ) : (
            <Button ghost onClick={this.cancel} style={{ width: 120 }}>
              取消
            </Button>
          )}
        </div>
      </div>
    );
  }
}
export default compose(
  withApollo,
  graphql(CreateUpdateFeedback, {
    name: 'createUpdateFeedback'
  })
)(FeedbackEdit);
