class Classification extends Component {
  renderPie = () => {
    const { data } = this.props;
    if (data.error) return <Network />;
    if (data.loading)
      return (
        <div className={styles.flexCol}>
          <Spin />
        </div>
      );
    const { alertStatisticPie } = data;
    return (
      <div className={styles.flex}>
        {alertStatisticPie.map((value, index) => {
          return <PieChart data={value.data} title={value.title} key={index} />;
        })}
      </div>
    );
  };
  render() {
    return (
      <div className={styles.panel}>
        <div className={styles.flex}>
          <span className={styles.title}>预警分类统计</span>
        </div>
        {this.renderPie()}
      </div>
    );
  }
}
export default compose(
  withApollo,
  graphql(AlertStatisticPie, {
    options: props => {
      return {
        variables: {
          fanId: props.fanId,
          startTime: props.startTime,
          endTime: props.endTime,
          parentAreaId: props.parentAreaId,
          sonAreaId: props.sonAreaId
        },
        errorPolicy: 'all',
        fetchPolicy: 'cache-and-network',
        notifyOnNetworkStatusChange: true
      };
    }
  })
)(Classification);
