import React, { Component } from 'react';
import styles from './index.less';
import ReactEcharts from 'echarts-for-react';
// import DataSource from 'Resource/json/statisticsChart.json';
import { compose, graphql, withApollo } from 'react-apollo';
import { AlertNumberTimeSeries } from 'Api/graphql/alertRecord.graphql';
import Network from 'Components/Network';
import { Spin } from 'antd';

class LineChart extends Component {
  formatOption = data => {
    return {
      color: {
        type: 'linear',
        x: 0,
        y: 0,
        x2: 1,
        y2: 0,
        colorStops: [
          {
            offset: 0,
            color: '#FFC200' // 0% 处的颜色
          },
          {
            offset: 1,
            color: '#FE4719' // 100% 处的颜色
          }
        ],
        globalCoord: false // 缺省为 false
      },
      grid: {
        left: 60,
        right: 20
      },
      xAxis: {
        type: 'category',
        data: data.keys || [],
        symbolOffset: ['50%', 0]
      },
      yAxis: {
        type: 'value',
        name: '次',
        minInterval: 1
      },
      tooltip: {
        show: true,
        trigger: 'axis'
      },
      series: [
        {
          data: data.data,
          type: 'line',
          symbol: 'none',
          name: ''
        }
      ]
    };
  };
  renderContent = () => {
    const { data } = this.props;
    if (data.error) return <Network />;
    if (data.loading)
      return (
        <div className={styles.flexCol}>
          <Spin />
        </div>
      );
    const { alertNumberTimeSeries } = data;
    let option = this.formatOption(alertNumberTimeSeries);
    return (
      <ReactEcharts
        option={option}
        style={{ width: '100%', height: 315 }}
        theme="my_theme"
      />
    );
  };
  render() {
    return (
      <div>
        <div className={styles.flex}>
          <span className={styles.title}>预警趋势统计</span>
        </div>
        {this.renderContent()}
      </div>
    );
  }
}
export default compose(
  withApollo,
  graphql(AlertNumberTimeSeries, {
    options: props => {
      return {
        variables: {
          fanId: props.fanId,
          startTime: props.startTime,
          endTime: props.endTime,
          parentAreaId: props.parentAreaId,
          sonAreaId: props.sonAreaId
        },
        errorPolicy: 'all',
        fetchPolicy: 'cache-and-network',
        notifyOnNetworkStatusChange: true
      };
    }
  })
)(LineChart);
