import React, { Component } from 'react';
import styles from './index.less';
import { withRouter } from 'react-router-dom';
import { Divider, Icon, Spin } from 'antd';
import Fan from '../Fan/Test';
import eventObject from 'Configs/eventSignal';
// import checkPointData from 'Resource/json/checkPointData.json';
import className from 'classnames';
import double_suction from './doubleSuction.js';
import single_suction from './single_suction.js';
import cantilever from './cantilever.js';
import axial_flow from './axial_flow.js';
import { compose, graphql, withApollo } from 'react-apollo';
import { ComponentStatusData } from 'Api/graphql/deviceDetail.graphql';
import { getStore } from '../../../../api/util';
import RealTimeData from '../RealTimeData';
let cx = className.bind(styles);

@withRouter
class Health extends Component {
  constructor(props) {
    super(props);
    this.state = {
      current: '',
      move: true,
      visible: false
    };
    this.obj = this.getModal(props.iconType);
    this.params = { current: '', move: false };
  }
  componentDidMount() {
    this.formatData(this.props);
    eventObject.changeFan.add(this.changeFan);
  }
  componentWillReceiveProps(nextProps) {
    this.formatData(nextProps);
  }
  componentWillUnmount() {
    eventObject.changeFan.remove(this.changeFan);
  }
  formatData = props => {
    this.obj = this.getModal(props.iconType);
    const { data } = props;
    if (data.error || data.loading) return null;
    data.componentStatusData.statusLt.map((value, index) => {
      return this.obj.map(item => {
        if (value.component.name === item.name) {
          item.status = value.status.name;
          return item;
        }
      });
    });
  };
  getModal = type => {
    return {
      double_suction: double_suction,
      single_suction: single_suction,
      axial_flow: axial_flow,
      cantilever: cantilever
    }[type];
  };
  move = name => {
    this.params.current = name;
    this.params.move = true;
    eventObject.mouse.dispatch(this.params);
  };
  out = () => {
    this.params.move = false;
    eventObject.mouse.dispatch(this.params);
  };
  scale = params => {
    if (params) {
      this.container.style.width = '100%';
    } else {
      this.container.style.width = 'calc(100% - 633px)';
    }
    this.setState({ visible: params });
    this.props.toggle(params);
    eventObject.scale.dispatch(params);
  };
  changeFan = (id, iconType) => {
    this.obj = this.getModal(iconType);
    eventObject.changeModal.dispatch(this.obj);
  };
  mouseEnter = (e, id, flag) => {
    e.stopPropagation();
    eventObject.mouseEnter.dispatch(id, flag);
  };
  renderCheckPoint = () => {
    const { data } = this.props;
    if (data.error) return null;
    if (data.loading)
      return (
        <div style={{ position: 'absolute', top: '50%', left: '49%' }}>
          <Spin />
        </div>
      );
    return data.componentStatusData.statusLt.map((value, index) => {
      return (
        <div
          className={cx(
            styles.card,
            styles[`location${index}`],
            styles[value.component.isShowParam ? 'test' : '']
          )}
          onMouseOver={() => this.move(value.component.name)}
          onMouseEnter={e =>
            this.mouseEnter(e, value.component.id, value.component.isShowParam)
          }
          onMouseOut={() => this.out()}
          key={index}
        >
          <span className={styles.name}>{value.component.displayName}</span>
          <span className={styles.content}>
            <span className={styles[value.status.name]} />
            {value.status.displayName}
          </span>
          <span className={styles.content}>
            数据点位：{value.component.paramNum}
          </span>
          <RealTimeData className={styles.floatWrap} fanId={this.props.fanId} />
        </div>
      );
    });
  };
  renderFanCard = () => {
    const { data } = this.props;
    if (data.error || data.loading) return null;
    return (
      <Fan
        obj={this.obj}
        rotate={data.componentStatusData.fanState}
        {...this.state}
      />
    );
  };
  render() {
    return (
      <div className={styles.healthWrap} ref={ref => (this.container = ref)}>
        <div className={styles.top}>
          <div>
            <span className={styles.title}>实时设备健康状态</span>
            <Divider type={'vertical'} style={{ height: 20 }} />
            <span>
              {getStore('name')}（{getStore('number')}）
            </span>
          </div>

          {!this.state.visible ? (
            <Icon type="fullscreen" onClick={() => this.scale(true)} />
          ) : (
            <Icon type="fullscreen-exit" onClick={() => this.scale(false)} />
          )}
        </div>
        {this.renderFanCard()}
        {this.renderCheckPoint()}
      </div>
    );
  }
}
export default compose(
  withApollo,
  graphql(ComponentStatusData, {
    options: props => {
      return {
        variables: { fanId: props.fanId },
        errorPolicy: 'all',
        fetchPolicy: 'cache-and-network'
      };
    }
  })
)(Health);
