import React, { Component } from 'react';
import styles from './index.less';
import List from './list';
import { Popover, Badge } from 'antd';
import { withRouter } from 'react-router-dom';
import Routes from '../../routes';
// import AlertData from 'Resource/json/alert.json';
import { compose, graphql, withApollo } from 'react-apollo';
import { LatestMessage } from 'Api/graphql/deviceList.graphql';

@withRouter
class Container extends Component {
  constructor(props) {
    super(props);
    this.isAllRead = false;
    this.content = null;
    this.message = [];
  }
  componentDidMount() {
    this.formatData(this.props);
  }

  componentWillReceiveProps(nextProps) {
    this.formatData(nextProps);
  }
  formatData = props => {
    const { data } = props;
    if (data.error || data.loading) return null;
    this.isAllRead = data.latestMessage.isAllRead;
    this.message = data.latestMessage.message;
    this.content = (
      <div className={styles.wrap}>
        <div className={styles.top}>
          <span style={{ width: 28 }} />
          <span className={styles.title}>最新预警</span>
          <span onClick={this.go} className={styles.all}>
            全部
          </span>
        </div>
        <List data={this.message} />
      </div>
    );
  };
  //查看全部
  go = () => {
    this.props.history.push('/AlertRecord');
  };
  render() {
    return (
      <div className={styles.container}>
        <header className={styles.header}>
          <div className={styles.headLeft}>
            <span className={styles.back} />
            <span>风机设备集中监控</span>
          </div>
          <div className={styles.headRight}>
            <Popover content={this.content} placement="bottom">
              <Badge dot={!this.isAllRead}>
                <span className={styles.message} />
              </Badge>
            </Popover>
            <span className={styles.user} />
            <span>用户名</span>
          </div>
        </header>
        <div className={styles.content}>
          <Routes />
        </div>
      </div>
    );
  }
}
export default compose(
  withApollo,
  graphql(LatestMessage, {
    options: props => {
      return {
        errorPolicy: 'all',
        fetchPolicy: 'cache-and-network'
      };
    }
  })
)(Container);
