import React, { Component } from 'react';
import styles from './index.less';
import classNames from 'classnames';
import { withRouter } from 'react-router-dom';
import { compose, graphql, withApollo } from 'react-apollo';
import { ComponentMessage } from 'Api/graphql/alertDetail.graphql';
import Network from '../../../components/Network';
import { Spin } from 'antd';
import eventObject from 'Configs/eventSignal';

const cx = classNames.bind(styles);
@withRouter
class AlertDetail extends Component {
  componentDidMount() {
    eventObject.status.add(this.fetchData);
  }
  componentWillUnmount() {
    eventObject.status.remove(this.fetchData);
  }
  fetchData = () => {
    this.props.data.refetch();
  };
  goBack = () => {
    this.props.history.goBack();
  };
  renderContent = () => {
    const { data } = this.props;
    if (data.error) return <Network />;
    if (data.loading)
      return (
        <div className={styles.flexCol}>
          <Spin />
        </div>
      );
    const { componentMessage } = data;
    return (
      <div className={styles.textWrap}>
        <div className={styles.number}>{componentMessage.areaFanInfo}</div>
        <div className={styles.text}>
          <span className={cx(styles.title, styles[componentMessage.level])}>
            {componentMessage.reason}
          </span>
          <p style={{ marginBottom: 10 }}>
            <span style={{ width: 300, display: 'inline-block' }}>
              预警编号：{componentMessage.troubleNumber}
            </span>
            <span>当前状态：{componentMessage.reportStatus}</span>
          </p>
          <p style={{ marginBottom: 10 }}>
            <span style={{ width: 300, display: 'inline-block' }}>
              发生时间：{componentMessage.createTime}
            </span>
            <span>持续时间：{componentMessage.lastTime}</span>
          </p>
          <span className={styles.smallTitle}>预警详情</span>
          <div>
            {componentMessage.detail.map((value, index) => {
              return (
                <div key={index} className={styles.description}>
                  {index + 1}.
                  {value}
                </div>
              );
            })}
          </div>
        </div>
      </div>
    );
  };
  renderCircle = () => {
    const { data, iconType } = this.props;
    if (data.error || data.loading) return null;
    const { componentMessage } = data;
    return (
      <div
        className={cx(
          styles.circle,
          styles[iconType + '_' + componentMessage.componentName]
        )}
      />
    );
  };
  render() {
    const { iconType } = this.props;
    return (
      <div className={styles.container}>
        {/*<div className={styles.double_suction}>{this.renderCircle()}</div>*/}
        <div className={styles[iconType]}>{this.renderCircle()}</div>
        {this.renderContent()}
      </div>
    );
  }
}
export default compose(
  withApollo,
  graphql(ComponentMessage, {
    options: props => {
      return {
        variables: {
          messageId: props.id
        },
        errorPolicy: 'all',
        fetchPolicy: 'cache-and-network'
      };
    }
  })
)(AlertDetail);
