import React, { Component } from 'react';
import styles from './index.less';
import { Tree, Spin } from 'antd';
import Network from 'Components/Network';
import Empty from '../../../components/Empty';

const { TreeNode } = Tree;

export default class TreeFormat extends Component {
  onChange = (selectedKeys, e) => {
    if (!e.node.props.dataRef.children && selectedKeys.length) {
      this.props.onChange(selectedKeys, e);
    }
    // console.log(selectedKeys, e);
  };
  //树形结构加载
  renderTreeNodes = data => {
    return data.map((item, index) => {
      // console.log(index);
      if (item.children) {
        return (
          <TreeNode
            title={item.label}
            key={item.key}
            dataRef={item}
            value={item.value}
          >
            {this.renderTreeNodes(item.children, item.value)}
          </TreeNode>
        );
      }
      return (
        <TreeNode
          title={
            <span>
              {item.label} <span className={styles[`${item.status}Circle`]} />
            </span>
          }
          key={item.key}
          dataRef={item}
          value={item.value}
        />
      );
    });
  };
  render() {
    const { statisticAndCheckpointTree } = this.props;
    if (statisticAndCheckpointTree.error)
      return (
        <div className={styles.flexCol}>
          <Network />
        </div>
      );
    if (statisticAndCheckpointTree.loading)
      return (
        <div className={styles.flexCol}>
          <Spin />
        </div>
      );
    const treeData = statisticAndCheckpointTree.statisticAndCheckpointTree;
    return (
      <div className={styles.listContainer}>
        <div className={styles.flex}>设备名称：{treeData.name}</div>
        <div className={styles.flex}>
          设备编码：{treeData.fanNumber}/{treeData.motorNumber}
        </div>
        <div className={styles.flex}>数据点位总数：{treeData.paramCount}</div>
        {treeData.checkpointTree.length ? (
          <Tree
            onSelect={this.onChange}
            style={{
              borderTop: '1px dashed',
              borderColor: 'rgba(255,255,255,.5)'
            }}
            defaultExpandedKeys={this.props.treeValue}
            defaultSelectedKeys={this.props.treeValue}
          >
            {this.renderTreeNodes(treeData.checkpointTree)}
          </Tree>
        ) : (
          <Empty style={{ marginTop: 200 }} />
        )}
      </div>
    );
  }
}
